import {ModalActionPayload, ModalChangePayload} from '@here/hds-modal';
import React, {useEffect, useImperativeHandle, useRef} from 'react';
import {HDSModalProperties} from '../Modal/HDSModal.js';
import {Confirm} from '@here/hds-confirm';

// eslint-disable-next-line no-new
new Confirm();

export interface HDSConfirmProperties
  extends Omit<
    HDSModalProperties,
    | 'headingMessage'
    | 'message'
    | 'hideHeader'
    | 'hideFooter'
    | 'titleSize'
    | 'confirmVariant'
    | 'hideConfirm'
    | 'hideCancel'
    | 'hideThirdButton'
  > {}

const HDSConfirm = React.forwardRef(
  (
    {
      className,
      children,
      onClose,
      onChange,
      onConfirm,
      onThirdButton,
      onCancel,
      icon,
      iconCategory,
      iconColor,
      hideCloseBtn,
      header,
      footer,
      disableCloseOnOverlayClick,
      disableCloseOnEscape,
      confirmText,
      cancelText,
      thirdButtonText,
      ...props
    }: HDSConfirmProperties,
    ref
  ) => {
    const innerRef = useRef<HTMLElement>(null);

    useImperativeHandle(ref, () => innerRef.current);
    const _handleClose = (e: CustomEvent<ModalChangePayload>) => {
      if (e.target !== innerRef.current) return;
      onClose && onClose(e.detail);
      onChange && onChange(e.detail);
    };
    const _handleAction = (e: CustomEvent<ModalActionPayload>) => {
      if (e.target !== innerRef.current) return;
      switch (e.detail.from) {
        case 'thirdButton':
          onThirdButton && onThirdButton();
          break;
        case 'confirm':
          onConfirm && onConfirm();
          break;
        case 'cancel':
          onCancel && onCancel();
          break;
      }
    };

    useEffect(() => {
      if (innerRef.current) {
        (onChange || onClose) &&
          innerRef.current.addEventListener('change', _handleClose as EventListener);
        (onConfirm || onCancel || onThirdButton) &&
          innerRef.current.addEventListener('action', _handleAction as EventListener);
      }
      return () => {
        innerRef.current?.removeEventListener('close', _handleClose as EventListener);
        innerRef.current?.removeEventListener('action', _handleAction as EventListener);
      };
    }, [onClose, onChange, onConfirm, onThirdButton, onCancel, innerRef]);

    const attributes: HDSConfirmProperties = {};
    icon && (attributes.icon = icon);
    iconCategory && (attributes['icon-category'] = iconCategory);
    iconColor && (attributes['icon-color'] = iconColor);
    hideCloseBtn && (attributes['hide-close-btn'] = hideCloseBtn);
    disableCloseOnOverlayClick &&
      (attributes['disable-close-on-overlay-click'] = disableCloseOnOverlayClick);
    disableCloseOnEscape && (attributes['disable-close-on-escape'] = disableCloseOnEscape);
    confirmText && (attributes['confirm-text'] = confirmText);
    cancelText && (attributes['cancel-text'] = cancelText);
    thirdButtonText && (attributes['third-button-text'] = thirdButtonText);

    return (
      <hds-confirm {...props} {...attributes} {...(className && {class: className})} ref={innerRef}>
        <span slot="header">{header}</span>
        <div slot="body">{children}</div>
        {footer && <div slot="footer">{footer}</div>}
      </hds-confirm>
    );
  }
);

HDSConfirm.defaultProps = {
  hideCloseBtn: false,
  closeAriaLabel: 'Close',
  icon: '',
  iconColor: '',
  iconCategory: 'core-ui',
  disableCloseOnOverlayClick: false,
  disableCloseOnEscape: false,
  confirmText: 'Done',
  cancelText: 'Cancel',
  thirdButtonText: 'Settings',
};

export default HDSConfirm;
