import {IconCategory} from '@here/hds-iconlibrary';
import {useEffect, useImperativeHandle} from 'react';
import * as React from 'react';
import {Icon, IconNotFoundPayload, IconStyle} from '@here/hds-icon';

// eslint-disable-next-line no-new
new Icon();

interface HDSIconProperties extends Omit<React.HTMLAttributes<HTMLElement>, 'onError'> {
  name: string;
  category?: IconCategory;
  iconStyle?: IconStyle;
  size?: string;
  // eslint-disable-next-line no-unused-vars
  onNotFound?: (e: IconNotFoundPayload) => void;
}

const HDSIcon = React.forwardRef((props: HDSIconProperties, ref) => {
  const {iconStyle, className, onNotFound, ...rest} = props;

  const innerRef = React.useRef<HTMLElement>(null);
  useImperativeHandle(ref, () => innerRef.current);

  const _handleNotFound = (e: CustomEvent<IconNotFoundPayload>) => {
    if (e.target !== innerRef.current) return;
    onNotFound && onNotFound(e.detail);
  };

  useEffect(() => {
    if (onNotFound && innerRef.current) {
      innerRef.current.addEventListener('notfound', _handleNotFound as EventListener);
    }
    return () =>
      innerRef.current?.removeEventListener('notfound', _handleNotFound as EventListener);
  }, [onNotFound, innerRef]);

  return (
    <hds-icon
      {...(className && {class: className})}
      {...(iconStyle && {'icon-style': iconStyle})}
      ref={innerRef}
      {...rest}
    ></hds-icon>
  );
});

HDSIcon.defaultProps = {
  category: 'core-ui',
  iconStyle: 'solid',
  size: '16px',
};

export default HDSIcon;
