import {SwitcherVariant, SwitcherSize, Switcher, SwitcherSelectPayload} from '@here/hds-switcher';
import React, {useEffect, useImperativeHandle, useRef} from 'react';

// eslint-disable-next-line no-new
new Switcher();

interface HDSSwitcherProps extends Omit<React.HTMLAttributes<HTMLElement>, 'onSelect'> {
  variant?: SwitcherVariant;
  size?: SwitcherSize;
  disabled?: boolean;
  ariaLabel?: string;
  onSelect?: (tabIndex: SwitcherSelectPayload) => void;
}

const HDSSwitcher = React.forwardRef((props: HDSSwitcherProps, ref) => {
  const {children, className, disabled, variant, size, ariaLabel, onSelect, ...rest} = props;
  const innerRef = useRef<HTMLElement>(null);

  useImperativeHandle(ref, () => innerRef.current);

  const _handleSelected = (e: CustomEvent<SwitcherSelectPayload>) => {
    if (e.target !== innerRef.current) return;
    onSelect && onSelect(e.detail);
  };

  useEffect(() => {
    if (innerRef.current) {
      if (onSelect) {
        innerRef.current.addEventListener('select', _handleSelected as EventListener);
      }
    }
    return () => {
      innerRef.current?.removeEventListener('select', _handleSelected as EventListener);
    };
  }, [onSelect, innerRef]);

  return (
    <hds-switcher
      {...(className && {class: className})}
      {...(variant && {variant})}
      {...(size && {size})}
      {...(disabled && {disabled})}
      {...(ariaLabel && {'aria-label': ariaLabel})}
      class={className}
      ref={innerRef}
      {...rest}
    >
      {children}
    </hds-switcher>
  );
});

HDSSwitcher.defaultProps = {
  disabled: false,
  variant: 'subtle',
  size: 'medium',
  ariaLabel: undefined,
};

export default HDSSwitcher;
