import {customElement, BaseElement} from '@here/hds-base';
import '@here/hds-checkbox';
import '@here/hds-icon';
import {html, LitElement, nothing} from 'lit';
import {property, query} from 'lit/decorators.js';
import {classMap} from 'lit/directives/class-map.js';
import {styles} from './hds-option.css.js';

export type OptionVariant = 'single' | 'multiple';

/**
 * @slot default Content for the label
 */
@customElement('hds-option')
export class Option extends BaseElement {
  static styles = styles;
  static shadowRootOptions = {...LitElement.shadowRootOptions, delegatesFocus: true};
  isTabbable = true;
  @property({type: Boolean}) disabled = false;
  @property({type: Boolean, reflect: true}) selected = false;
  @property({type: String, attribute: 'variant'}) variant: OptionVariant = 'single';
  @query('slot') protected optionContentSlot!: HTMLSlotElement;

  protected render() {
    const classes = {
      selected: this.selected,
      disabled: this.disabled,
    };

    const optionTemplate =
      this.variant === 'single'
        ? this.renderSingleOption(this.selected)
        : this.renderMultipleOption(this.selected, this.disabled);

    return html`
      <div class="-hds-option-wrapper ${classMap(classes)}" tabindex="-1">${optionTemplate}</div>
    `;
  }

  protected renderIcon() {
    return html` <hds-icon name="checkmark" size="16px"></hds-icon>`;
  }

  protected renderSingleOption(isSelected: boolean) {
    return html`
      <div class="single-variant">
        <div class="option-text">
          <slot></slot>
        </div>
        ${isSelected ? this.renderIcon() : nothing}
      </div>
    `;
  }

  protected renderMultipleOption(isSelected: boolean, isDisabled: boolean) {
    return html`
      <div class="multiple-variant">
        <hds-checkbox
          ?checked="${isSelected}"
          ?disabled="${isDisabled}"
          @click="${e => e.preventDefault()}"
        >
        </hds-checkbox>
        <div class="option-text">
          <slot></slot>
        </div>
      </div>
    `;
  }
}

declare global {
  interface HTMLElementTagNameMap {
    'hds-option': Option;
  }
}
