import {IconCategory} from '@here/hds-iconlibrary';
import React, {useEffect, useImperativeHandle, useRef} from 'react';
import {Chip, ChipSelectPayload, ChipSize} from '@here/hds-chip';

// eslint-disable-next-line no-new
new Chip();

interface HDSChipProperties extends Omit<React.HTMLAttributes<HTMLElement>, 'onSelect'> {
  size?: ChipSize;
  disabled?: boolean;
  selected?: boolean;
  dragging?: boolean;
  image?: string;
  avatar?: string;
  icon?: string;
  iconCategory?: IconCategory;
  maxWidth?: number;
  order?: number;
  selectable?: boolean;
  removable?: boolean;
  tooltip?: boolean;
  removeButtonFocused?: boolean;
  onRemove?: () => void;
  onSelect?: (selected: ChipSelectPayload) => void;
}

const HDSChip = React.forwardRef((props: HDSChipProperties, ref) => {
  const {
    className,
    size,
    disabled,
    selected,
    dragging,
    image,
    avatar,
    icon,
    iconCategory,
    maxWidth,
    order,
    removable,
    selectable,
    tooltip,
    removeButtonFocused,
    onRemove,
    onSelect,
    children,
    ...rest
  } = props;
  const innerRef = useRef<HTMLElement>(null);

  useImperativeHandle(ref, () => innerRef.current);

  const handleRemove = (e: CustomEvent) => {
    if (e.target !== innerRef.current) return;
    onRemove && onRemove();
  };

  const handleSelect = (e: CustomEvent<ChipSelectPayload>) => {
    if (e.target !== innerRef.current) return;
    onSelect && onSelect(e.detail);
  };

  useEffect(() => {
    if (innerRef.current) {
      if (onRemove) innerRef.current.addEventListener('remove', handleRemove as EventListener);
    }
    return () => {
      innerRef.current?.removeEventListener('remove', handleRemove as EventListener);
    };
  }, [onRemove, innerRef]);

  useEffect(() => {
    if (innerRef.current) {
      if (onSelect) {
        innerRef.current.addEventListener('select', handleSelect as EventListener);
      }
    }
    return () => {
      innerRef.current?.removeEventListener('select', handleSelect as EventListener);
    };
  }, [onSelect, innerRef]);

  return (
    <hds-chip
      {...(className && {class: className})}
      size={size}
      {...(disabled && {disabled: true})}
      {...(selected && {selected: true})}
      {...(dragging && {dragging: true})}
      image={image}
      avatar={avatar}
      icon={icon}
      {...(iconCategory && {'icon-category': iconCategory})}
      {...(maxWidth && {'max-width': maxWidth})}
      order={order}
      {...(removable && {removable: true})}
      {...(selectable && {selectable: true})}
      {...(tooltip && {tooltip: true})}
      {...(removeButtonFocused && {removeButtonFocused: true})}
      {...rest}
      ref={innerRef}
    >
      {children}
    </hds-chip>
  );
});

HDSChip.defaultProps = {
  size: 'medium',
  disabled: false,
  selected: false,
  dragging: false,
  selectable: false,
  order: 0,
  removable: false,
};

export default HDSChip;
