import {customElement, BaseElement} from '@here/hds-base';
import '@here/hds-icon';
import {IconCategory} from '@here/hds-iconlibrary';
import '@here/hds-tooltip';
import {html, LitElement, nothing, PropertyValues} from 'lit';
import {property, query} from 'lit/decorators.js';
import {classMap} from 'lit/directives/class-map.js';
import {ifDefined} from 'lit/directives/if-defined.js';
import {styles} from './hds-list-item.css.js';
import {ListPadding, ListVariant} from './hds-list.js';

export type ListItemSize = 'small' | 'medium' | 'large' | 'extra-large' | 'auto';
export type ListItemVariant = 'default' | 'success' | 'error';
export type ListItemSelectPayload = string;

/**
 * @slot default Content for the item label
 *
 * @event {CustomEvent<string>} select Fired when the element has been selected with the id of the list item `ListItemSelectPayload`
 */
@customElement('hds-list-item')
export class ListItem extends BaseElement {
  static styles = styles;
  static itemIdCounter = 0;
  static shadowRootOptions = {...LitElement.shadowRootOptions, delegatesFocus: true};
  isTabbable = true;
  // @property({type: Boolean}) disabled = false;
  @property({type: Boolean, attribute: false}) parentDisabled = false;
  @property({type: Boolean, reflect: true}) selected = false;
  @property({type: Boolean, reflect: true}) dragging = false;
  @property({type: String}) icon?: string;
  @property({type: String, attribute: 'icon-category'}) iconCategory: IconCategory = 'core-ui';
  @property({type: String, attribute: 'icon-right'}) iconRight?: string;
  @property({type: String, attribute: 'icon-right-category'}) iconRightCategory: IconCategory =
    'core-ui';
  @property({type: Boolean, attribute: false}) hasTooltip = false;
  @property({type: Boolean, attribute: false}) hasAnchor = false;
  @property({type: String}) variant: ListItemVariant = 'default';
  @property({type: String}) groupVariant: ListVariant = 'subtle';
  @property({type: String}) size: ListItemSize = 'small';
  @property({type: Boolean, attribute: false}) unhoverable = false;
  @property({type: Boolean, attribute: false}) unpressable = false;
  @property({type: Boolean, attribute: false}) unselectable = false;
  @property({type: Boolean, attribute: false}) unfocusable = false;
  @property({type: String, attribute: false}) horizontalPadding: ListPadding = 'default';
  @query('slot') protected listItemContentSlot!: HTMLSlotElement;
  @query('.text') protected textContainer!: HTMLElement;

  private _disabled = false;

  get disabled() {
    return this._disabled || this.parentDisabled;
  }

  @property({type: Boolean})
  set disabled(value: boolean) {
    const oldValue = this._disabled;
    this._disabled = value;
    this.requestUpdate('disabled', oldValue);
  }

  protected firstUpdated() {
    this.id = this.id || `hds-list-item-${(ListItem.itemIdCounter += 1)}`;
    this.setAttribute('data-hds-tooltip', this.id);

    if (this.textContainer && this.textContainer.scrollWidth > this.textContainer.offsetWidth) {
      this.hasTooltip = true;
    }
    const slotElements = this.listItemContentSlot?.assignedElements({flatten: true});
    const anchorTag = slotElements.find(element => element.nodeName === 'A') as HTMLAnchorElement;

    if (anchorTag && this.size !== 'auto') {
      this.hasAnchor = true;
      if (anchorTag.scrollWidth > anchorTag.offsetWidth) {
        this.hasTooltip = true;
      }
    }
  }

  protected updated(_changedProperties: PropertyValues) {
    if (
      _changedProperties.has('selected') &&
      !this.disabled &&
      this.selected &&
      !this.unselectable
    ) {
      this.updateComplete;
      this.dispatchEvent(
        new CustomEvent<ListItemSelectPayload>('select', {detail: this.id, bubbles: true})
      );
    }
  }

  protected render() {
    const classes = {
      item: true,
      selected: this.selected,
      disabled: this.disabled,
      dragging: this.dragging,
      [`${this.unhoverable ? 'unhoverable' : 'hoverable'}`]: true,
      [`${this.unselectable ? 'unselectable' : 'selectable'}`]: true,
      [`${this.unpressable ? 'unpressable' : 'pressable'}`]: true,
      [`${this.unfocusable ? 'unfocusable' : 'focusable'}`]: true,
      'hds-list-item--has-anchor': this.hasAnchor,
    };
    classes[`hds-list-item-variant-${this.groupVariant}-${this.variant}`] = true;
    classes[`hds-list-item-size-${this.size}`] = true;
    classes[`hds-list-item-h-padding-${this.horizontalPadding}`] = true;

    return html`
      <div
        class="-hds-list-item-wrapper ${classMap(classes)}"
        role="option"
        aria-selected="${ifDefined(this.selected)}"
        tabindex="0"
        @click="${this.onItemClick}"
        ?disabled="${this.disabled}"
      >
        ${this.renderIcon()}
        <span class="text">
          <slot></slot>
        </span>
        <slot name="right"></slot>
        ${this.renderIconRight()}
      </div>
      ${this.renderTooltip()}
    `;
  }

  protected renderIcon() {
    if (this.icon) {
      return html`
        <hds-icon
          class="icon icon-left"
          name="${this.icon}"
          category="${this.iconCategory}"
          size="16px"
        ></hds-icon>
      `;
    }
    return nothing;
  }

  protected renderIconRight() {
    if (this.iconRight) {
      return html`
        <hds-icon
          class="icon icon-right"
          name="${this.iconRight}"
          category="${this.iconRightCategory}"
          size="16px"
        ></hds-icon>
      `;
    }
    return nothing;
  }

  protected async onItemClick() {
    if (!this.disabled && !this.unselectable) {
      this.selected = true;
    }
  }

  private renderTooltip() {
    if (this.hasTooltip) {
      return html`
        <hds-tooltip id="${this.id}" position="right">
          <div>${this.listItemContentSlot.assignedNodes()[0]?.textContent}</div>
        </hds-tooltip>
      `;
    }
    return nothing;
  }
}

declare global {
  interface HTMLElementTagNameMap {
    'hds-list-item': ListItem;
  }
}
