import {getCSSVariable} from '@here/hds-base';

export interface Breakpoint {
  name: string;
  minWidth?: string | number;
  maxWidth?: string | number;
  rowGap: number | string;
  columnGap: number | string;
  columns: number;
}

export const DefaultBreakpoints: Breakpoint[] = [
  {
    name: 'mobile',
    maxWidth: 600,
    rowGap: 10,
    columnGap: 10,
    columns: 4,
  },
  {
    name: 'tablet',
    minWidth: 601,
    maxWidth: 1024,
    rowGap: 10,
    columnGap: 10,
    columns: 6,
  },
  {
    name: 'desktop-small',
    minWidth: 1025,
    maxWidth: 1440,
    rowGap: 10,
    columnGap: 10,
    columns: 12,
  },
  {
    name: 'desktop-medium',
    minWidth: 1441,
    maxWidth: 1680,
    rowGap: 10,
    columnGap: 10,
    columns: 12,
  },
  {
    name: 'desktop-large',
    minWidth: 1681,
    rowGap: 10,
    columnGap: 10,
    columns: 12,
  },
];

export class HDSGrid {
  static breakpointNames = DefaultBreakpoints.map(e => e.name);

  static getWidthForBreakpoint(name: string, el: HTMLElement): number | string | undefined {
    if (!this.breakpointNames.includes(name)) {
      // eslint-disable-next-line no-console
      console.error('Invalid breakpoint name');
    }

    const {minWidth} = HDSGrid.breakpoint(name, el);
    if (minWidth && typeof minWidth === 'number') {
      const baseFontSize = parseInt(getCSSVariable('--hds-font-baseline', el), 10);
      return minWidth * baseFontSize;
    }
    return minWidth;
  }

  static breakpoints(el: HTMLElement): {[key: string]: Breakpoint} {
    const brs: {[key: string]: Breakpoint} = {};
    for (const name of this.breakpointNames) {
      brs[name] = HDSGrid.breakpoint(name, el);
    }
    return brs;
  }

  static breakpoint(name: string, el: HTMLElement): Breakpoint {
    return {
      name,
      columns: parseInt(getCSSVariable(`--hds-grid-columns-${name}`, el), 10),
      minWidth: parseInt(getCSSVariable(`--hds-grid-min-width-${name}`, el), 10),
      maxWidth: parseInt(getCSSVariable(`--hds-grid-max-width-${name}`, el), 10),
      columnGap: parseInt(getCSSVariable(`--hds-grid-column-gap-${name}`, el), 10),
      rowGap: parseInt(getCSSVariable(`--hds-grid-row-gap-${name}`, el), 10),
    };
  }
}
