import {ListSelectPayload} from '@here/hds-list';
import {RichList, RichListItemActionPayload, RichListItemVariant} from '@here/hds-rich-list';
import React, {useEffect, useImperativeHandle} from 'react';
import {HDSListProperties} from '../List/HDSList.js';

// eslint-disable-next-line no-new
new RichList();

interface HDSRichListProperties extends Omit<HDSListProperties, 'variant'> {
  children: React.ReactNode;
  disabled?: boolean;
  variant?: RichListItemVariant;
  onAction?: (payload: RichListItemActionPayload) => void;
}

const HDSRichList = React.forwardRef((props: HDSRichListProperties, ref) => {
  const {children, variant, onAction, onSelect, disabled, className, ...rest} = props;
  const innerRef = React.useRef<HTMLElement>(null);
  useImperativeHandle(ref, () => innerRef.current);

  const _handleAction = (e: CustomEvent<RichListItemActionPayload>) => {
    onAction && onAction(e.detail);
  };

  useEffect(() => {
    if (onAction && innerRef.current) {
      innerRef.current.addEventListener('action', _handleAction as EventListener);
    }
    return () => innerRef.current?.removeEventListener('action', _handleAction as EventListener);
  }, [onAction, innerRef]);

  const _handleSelected = (e: CustomEvent<ListSelectPayload>) => {
    if (e.target !== innerRef.current) return;
    onSelect && onSelect(e.detail);
  };
  useEffect(() => {
    if (onSelect && innerRef.current) {
      innerRef.current.addEventListener('select', _handleSelected as EventListener);
    }
    return () => innerRef.current?.removeEventListener('select', _handleSelected as EventListener);
  }, [onSelect, innerRef]);

  return (
    <hds-rich-list
      ref={innerRef}
      variant={variant}
      {...(disabled && {disabled})}
      {...(className && {class: className})}
      {...rest}
    >
      {children}
    </hds-rich-list>
  );
});

HDSRichList.defaultProps = {
  disabled: false,
  variant: 'default',
};

export default HDSRichList;
