import React, {useEffect, useRef, useImperativeHandle} from 'react';
import {Checkbox, CheckboxSize, CheckboxChangePayload} from '@here/hds-checkbox';

// eslint-disable-next-line no-new
new Checkbox();

export interface HDSCheckboxProperties extends Omit<React.HTMLAttributes<HTMLElement>, 'onChange'> {
  checked?: boolean;
  value?: string;
  size?: CheckboxSize;
  disabled?: boolean;
  id?: string;
  ariaLabel?: string;
  name?: string;
  description?: React.ReactNode;
  showRequired?: boolean;
  required?: boolean;
  autocomplete?: string;
  onCheck?: (detail: CheckboxChangePayload) => void;
  onChange?: (event: CustomEvent<CheckboxChangePayload>) => void;
}

const HDSCheckbox = React.forwardRef(
  (
    {
      children,
      checked,
      value,
      size,
      disabled,
      id,
      name,
      description,
      ariaLabel,
      className,
      onCheck,
      onChange,
      showRequired,
      required,
      autocomplete,
      ...rest
    }: HDSCheckboxProperties,
    ref
  ) => {
    const innerRef = useRef<HTMLElement>(null);

    useImperativeHandle(ref, () => innerRef.current);

    const _handleChange = (e: CustomEvent<CheckboxChangePayload>) => {
      if (e.target !== innerRef.current) return;
      onCheck && onCheck(e.detail);
      onChange && onChange(e);
    };

    useEffect(() => {
      if (innerRef.current && (onCheck || onChange)) {
        innerRef.current.addEventListener('change', _handleChange as EventListener);
      }
      return () => {
        innerRef.current?.removeEventListener('change', _handleChange as EventListener);
      };
    }, [onCheck, onChange, innerRef]);

    return (
      <hds-checkbox
        {...(checked && {checked: true})}
        {...(disabled && {disabled: true})}
        {...(showRequired && {'show-required': showRequired})}
        {...(className && {class: className})}
        {...(autocomplete && {autocomplete})}
        {...(ariaLabel && {'aria-label': ariaLabel})}
        value={value}
        size={size}
        required={required}
        id={id}
        name={name}
        ref={innerRef}
        {...rest}
      >
        {children}
        {description && <span slot="description">{description}</span>}
      </hds-checkbox>
    );
  }
);

HDSCheckbox.defaultProps = {
  size: 'medium',
  checked: false,
  disabled: false,
  required: false,
  showRequired: false,
  name: '',
  autocomplete: '',
  ariaLabel: '',
};

export default HDSCheckbox;
