import {customElement} from '@here/hds-base';
import {html, LitElement} from 'lit';
import {property} from 'lit/decorators.js';
import {classMap} from 'lit/directives/class-map.js';
import {
  NotificationActionPayload,
  NotificationChangePayload,
  NotificationSize,
  NotificationStatus,
  NotificationVariant,
} from './hds-notification.js';
import {styles} from './hds-toast-container.css.js';

export type NotificationPosition =
  | 'top-left'
  | 'top-center'
  | 'top-right'
  | 'bottom-left'
  | 'bottom-center'
  | 'bottom-right';

@customElement('hds-toast-container')
export class ToastContainer extends LitElement {
  static styles = styles;

  @property({type: String, reflect: true}) position: NotificationPosition = 'bottom-right';

  protected render() {
    const classes = {
      [`position-${this.position}`]: true,
    };
    return html` <div id="hds-notification__container" class="wrapper ${classMap(classes)}">
      <slot></slot>
    </div>`;
  }
}

export interface ToastProps {
  title: string;
  body?: string;
  bodyCssSelector?: string;
  variant?: NotificationVariant;
  status?: NotificationStatus;
  size?: NotificationSize;
  timeout?: number;
  position?: NotificationPosition;
  onChange?: (e: CustomEvent<NotificationChangePayload>) => void;
  onAction?: (e: CustomEvent<NotificationActionPayload>) => void;
}

export const toast = ({
  title,
  body = '',
  bodyCssSelector = '',
  timeout = 3,
  position = 'bottom-right',
  variant = 'solid',
  status = 'default',
  size = 'medium',
  onChange,
  onAction,
}: ToastProps) => {
  let parentEl = document.querySelector("[data-theme^='hds-']");
  if (!parentEl) {
    // eslint-disable-next-line no-console
    console.error("No [data-theme^='hds-'] element found.");
    parentEl = document.body;
  }

  const toastNotificationEl = document.createElement('hds-notification');
  toastNotificationEl.open = true;
  toastNotificationEl.headline = title;
  if (onChange) {
    toastNotificationEl.addEventListener('change', e =>
      onChange(e as CustomEvent<NotificationChangePayload>)
    );
  }
  if (onAction) {
    toastNotificationEl.addEventListener('action', e => onAction(e as CustomEvent));
  }
  if (bodyCssSelector) {
    const templateEl = document.querySelector(bodyCssSelector) as HTMLTemplateElement;
    if (templateEl) {
      const templateClone = templateEl.content.cloneNode(true);
      toastNotificationEl.appendChild(templateClone);
    } else {
      // eslint-disable-next-line no-console
      console.error(`No template element found with selector: ${bodyCssSelector}`);
      return;
    }
  } else {
    toastNotificationEl.innerHTML = body;
  }
  toastNotificationEl.variant = variant;
  toastNotificationEl.timeout = timeout;
  toastNotificationEl.status = status;
  toastNotificationEl.size = size;

  let container = document.querySelector('hds-toast-container');

  if (!container) {
    container = document.createElement('hds-toast-container');
    container.position = position;
    parentEl.appendChild(container);
  }
  container.appendChild(toastNotificationEl);
};

declare global {
  interface HTMLElementTagNameMap {
    'hds-toast-container': ToastContainer;
  }
}
