import React, {useRef, useEffect, useImperativeHandle} from 'react';
import {
  Notification,
  NotificationChangePayload,
  NotificationVariant,
  NotificationStatus,
  NotificationSize,
  NotificationActionPayload,
} from '@here/hds-notification';

// eslint-disable-next-line no-new
new Notification();

export interface HDSNotificationBaseProperties
  extends Omit<React.HTMLAttributes<HTMLDivElement>, 'onClose' | 'onChange'> {
  open?: boolean;
  headline?: string;
  variant?: NotificationVariant;
  status?: NotificationStatus;
  size?: NotificationSize;
  timeout?: number;
  keepNode?: boolean;
  hideClose?: boolean;
  hideIcon?: boolean;
  closeAriaLabel?: string;
  /* eslint-disable no-unused-vars */
  onClose?: (e: NotificationChangePayload) => void;
  onChange?: (e: CustomEvent<NotificationChangePayload>) => void;
  onAction?: (e: NotificationActionPayload) => void;
  /* eslint-enable no-unused-vars */
}

export interface HDSNotificationProperties extends HDSNotificationBaseProperties {
  children?: React.ReactNode;
  inline?: boolean;
}

const HDSNotification = React.forwardRef((props: HDSNotificationProperties, ref) => {
  const {
    children,
    variant,
    status,
    size,
    hideIcon,
    timeout,
    open,
    keepNode,
    inline,
    hideClose,
    closeAriaLabel,
    onClose,
    onChange,
    onAction,
    className,
    ...rest
  } = props;

  const innerRef = useRef<HTMLElement>(null);

  useImperativeHandle(ref, () => innerRef.current);

  const _handleClose = (e: CustomEvent<NotificationChangePayload>) => {
    if (e.target !== innerRef.current) return;
    onClose && onClose(e.detail);
    onChange && onChange(e);
  };
  const _handleActionClick = (e: CustomEvent<NotificationActionPayload>) => {
    onAction && onAction(e.detail);
  };

  useEffect(() => {
    if (innerRef.current && (onClose || onChange)) {
      innerRef.current.addEventListener('change', _handleClose as EventListener);
    }
    return () => {
      innerRef.current?.removeEventListener('change', _handleClose as EventListener);
    };
  }, [onClose, onChange, innerRef]);

  useEffect(() => {
    if (innerRef.current && onAction) {
      innerRef.current.addEventListener('action', _handleActionClick as EventListener);
    }
    return () => {
      innerRef.current?.removeEventListener('action', _handleActionClick as EventListener);
    };
  }, [onAction, innerRef]);

  return (
    <hds-notification
      open={open}
      keep-node={keepNode}
      variant={variant}
      status={status}
      size={size}
      timeout={timeout}
      {...(className && {class: className})}
      {...(hideClose && {'hide-close': hideClose})}
      {...(hideIcon && {'hide-icon': hideIcon})}
      {...(closeAriaLabel && {'close-aria-label': closeAriaLabel})}
      {...(inline && {inline})}
      ref={innerRef}
      {...rest}
    >
      {children}
    </hds-notification>
  );
});

HDSNotification.defaultProps = {
  open: true,
  variant: 'solid',
  status: 'default',
  size: 'medium',
  timeout: 0,
  keepNode: true,
  hideIcon: false,
  hideClose: false,
};

export default HDSNotification;
