import {TabBar, TabBarSelectPayload, TabBarVariant} from '@here/hds-tabs';
import React, {useEffect, useImperativeHandle, useRef} from 'react';

// eslint-disable-next-line no-new
new TabBar();

interface HDSTabBarProps extends Omit<React.HTMLAttributes<HTMLElement>, 'onSelect'> {
  children: React.ReactNode;
  onSelect?: (tabIndex: TabBarSelectPayload) => void;
  selectFirstTab?: boolean;
  ariaLabel?: string;
  ariaRole?: string;
  variant?: TabBarVariant;
}

const HDSTabBar = React.forwardRef((props: HDSTabBarProps, ref) => {
  const {children, variant, className, onSelect, selectFirstTab, ariaLabel, ariaRole, ...rest} =
    props;
  const innerRef = useRef<HTMLElement>(null);

  useImperativeHandle(ref, () => innerRef.current);

  const _handleSelected = (e: CustomEvent<TabBarSelectPayload>) => {
    if (e.target !== innerRef.current) return;
    onSelect && onSelect(e.detail);
  };
  useEffect(() => {
    if (innerRef.current) {
      if (onSelect) {
        innerRef.current.addEventListener('select', _handleSelected as EventListener);
      }
    }
    return () => {
      innerRef.current?.removeEventListener('select', _handleSelected as EventListener);
    };
  }, [onSelect, innerRef]);

  return (
    <hds-tab-bar
      variant={variant}
      {...(selectFirstTab && {'select-first-tab': selectFirstTab})}
      {...(ariaRole && {'aria-role': ariaRole})}
      {...(className && {class: className})}
      aria-label={ariaLabel}
      ref={innerRef}
      {...rest}
    >
      {children}
    </hds-tab-bar>
  );
});

HDSTabBar.defaultProps = {
  variant: 'default',
  selectFirstTab: false,
  ariaLabel: undefined,
  ariaRole: 'tablist',
};

export default HDSTabBar;
