import {customElement} from '@here/hds-base';
import {IconStyle} from '@here/hds-icon'; // eslint-disable-line import/no-duplicates
import '@here/hds-icon'; // eslint-disable-line import/no-duplicates
import {IconCategory} from '@here/hds-iconlibrary';
import {html, LitElement} from 'lit';
import {property} from 'lit/decorators.js';
import {classMap} from 'lit/directives/class-map.js';
import {ifDefined} from 'lit/directives/if-defined.js';

import {styles} from './hds-avatar.css.js';

export type AvatarSize = 'small' | 'medium' | 'large' | 'x-large';

/**
 * @summary An avatar component.
 * @cssprop --hds-avatar-background - Controls the background of the avatar
 * @cssprop --hds-avatar-border-background - Controls the background of the avatar's border
 * @cssprop --hds-avatar-focus-ring-color - Controls the color of the focus ring
 * */
@customElement('hds-avatar')
export class Avatar extends LitElement {
  static styles = styles;

  @property({type: Boolean}) disabled = false;
  @property({type: Boolean}) borderless = false;
  @property({type: String, reflect: true}) size: AvatarSize = 'medium';
  @property({type: String}) name: string | undefined;
  @property({type: String, attribute: 'image-url'}) imageUrl: string | undefined;
  @property({type: String}) icon: string | undefined;
  @property({type: String, attribute: 'icon-category'}) iconCategory: IconCategory = 'core-ui';
  @property({type: String, attribute: 'icon-style'}) iconStyle: IconStyle = 'solid';

  /**
   * @example
   * this.getInitials('Jane Mary Doe'); // returns 'JM'
   * @example
   * this.getInitials('Jane'); // returns 'Ja'
   * @example
   * this.getInitials(' '); // returns ''
   */
  protected getInitials(name: string | undefined) {
    let initials = '';

    if (name !== undefined) {
      const words = name.trim().split(' ');
      if (words.length === 1) {
        initials = words[0].slice(0, 2);
      } else {
        initials = words[0].slice(0, 1) + words[1].slice(0, 1);
      }
    }

    return initials;
  }

  protected render() {
    const classes = {
      disabled: this.disabled,
      bordered: !this.borderless,
    };
    classes[this.size] = true;
    return html`
      <div
        class="-hds-avatar-wrapper ${classMap(classes)}"
        aria-hidden="true"
        aria-disabled="${ifDefined(this.disabled)}"
      >
        <div class="content">${this.renderContent()}</div>
        <div class="focus-ring"></div>
      </div>
    `;
  }

  private getIconSize() {
    switch (this.size) {
      case 'x-large':
        return '24px';
      case 'small':
        return '12px';
      default:
        return '16px';
    }
  }

  private renderContent() {
    if (this.imageUrl) {
      return html`<img
        src="${this.imageUrl}"
        alt="${ifDefined(this.name)}"
        class="avatar-image"
        @error="${() => (this.imageUrl = '')}"
      />`;
    }

    // Renders the initials if a name was provided.
    const initials = this.getInitials(this.name);
    if (initials) {
      return html`${initials}`;
    }

    // Otherwise renders the fallback profile icon.
    return html` <hds-icon
      class="avatar-icon"
      name="${this.icon || 'profile'}"
      category="${this.iconCategory}"
      size="${this.getIconSize()}"
      role="presentation"
      icon-style="${parseInt(this.getIconSize(), 10) > 16 ? this.iconStyle : 'solid'}"
      @error="${() => (this.icon = '')}"
    ></hds-icon>`;
  }
}

declare global {
  interface HTMLElementTagNameMap {
    'hds-avatar': Avatar;
  }
}
