import {IconCategory} from '@here/hds-iconlibrary';
import React, {useEffect, useImperativeHandle} from 'react';
import {Dropzone, DropzoneChangePayload, DropzoneOrientation} from '@here/hds-dropzone';

// eslint-disable-next-line no-new
new Dropzone();

interface HDSDropzoneProperties extends Omit<React.HTMLAttributes<HTMLElement>, 'onChange'> {
  progress?: boolean;
  error?: boolean;
  disabled?: boolean;
  multiple?: boolean;
  accept?: string;
  icon?: string;
  reset?: boolean;
  iconCategory?: IconCategory;
  orientation?: DropzoneOrientation;
  textDropzoneBrowseFiles?: string;
  textDropzoneDropFiles?: string;
  textDropzoneOr?: string;
  textDropzoneUploading?: string;
  textDropzoneDraggingOver?: string;
  textDropzoneFileRestrictions?: string;
  textAssistive?: string;
  textError?: string;
  textTitle?: string;
  textFileRestrictions?: string;
  onChange?: (e: DropzoneChangePayload) => void;
}

const HDSDropzone = React.forwardRef((props: HDSDropzoneProperties, ref) => {
  const {
    progress,
    error,
    disabled,
    multiple,
    accept,
    icon,
    iconCategory,
    orientation,
    reset,
    textDropzoneBrowseFiles,
    textDropzoneDropFiles,
    textDropzoneOr,
    textDropzoneUploading,
    textDropzoneDraggingOver,
    textDropzoneFileRestrictions,
    textAssistive,
    textError,
    textTitle,
    textFileRestrictions,
    className,
    onChange,
    ...rest
  } = props;
  const innerRef = React.useRef<HTMLElement>(null);
  useImperativeHandle(ref, () => innerRef.current);

  const _handleChange = (e: CustomEvent<DropzoneChangePayload>) => {
    if (e.target !== innerRef.current) return;
    onChange && onChange(e.detail);
  };

  useEffect(() => {
    if (onChange && innerRef.current) {
      innerRef.current.addEventListener('change', _handleChange as EventListener);
    }
    return () => innerRef.current?.removeEventListener('change', _handleChange as EventListener);
  }, [onChange, innerRef]);

  useEffect(() => {
    if (reset && innerRef.current) {
      (innerRef.current as Dropzone).reset();
    }
  }, [reset, innerRef]);

  const attributes: HDSDropzoneProperties = {};
  progress && (attributes.progress = true);
  error && (attributes.error = true);
  disabled && (attributes.disabled = true);
  multiple && (attributes.multiple = true);
  textDropzoneBrowseFiles && (attributes['text-dropzone-browse-files'] = textDropzoneBrowseFiles);
  textDropzoneDropFiles && (attributes['text-dropzone-drop-files'] = textDropzoneDropFiles);
  textDropzoneOr && (attributes['text-dropzone-or'] = textDropzoneOr);
  textDropzoneUploading && (attributes['text-dropzone-uploading'] = textDropzoneUploading);
  textDropzoneDraggingOver &&
    (attributes['text-dropzone-dragging-over'] = textDropzoneDraggingOver);
  textDropzoneFileRestrictions &&
    (attributes['text-dropzone-file-restrictions'] = textDropzoneFileRestrictions);
  textAssistive && (attributes['text-assistive'] = textAssistive);
  textError && (attributes['text-error'] = textError);
  textTitle && (attributes['text-title'] = textTitle);
  textFileRestrictions && (attributes['text-file-restrictions'] = textFileRestrictions);

  return (
    <hds-dropzone
      icon={icon}
      {...(iconCategory && {'icon-category': iconCategory})}
      {...(className && {class: className})}
      accept={accept}
      orientation={orientation}
      ref={innerRef}
      {...attributes}
      {...rest}
    ></hds-dropzone>
  );
});

HDSDropzone.displayName = 'hds-dropzone';
HDSDropzone.defaultProps = {
  progress: false,
  error: false,
  disabled: false,
  multiple: false,
  accept: '',
  icon: '',
  iconCategory: 'core-ui',
  orientation: 'horizontal',
  textDropzoneBrowseFiles: 'Browse files',
  textDropzoneDropFiles: 'Drop files here',
  textDropzoneOr: 'or',
  textDropzoneUploading: 'Uploading',
  textDropzoneDraggingOver: '',
  textDropzoneFileRestrictions: '',
  textAssistive: '',
  textError: '',
  textTitle: '',
  textFileRestrictions: '',
};

export default HDSDropzone;
