import {customElement, clamp} from '@here/hds-base';
import {html, LitElement, PropertyValues} from 'lit';
import {property, query} from 'lit/decorators.js';
import {classMap} from 'lit/directives/class-map.js';
import {styles} from './hds-progress-indicator.css.js';

export type ProgressIndicatorColor =
  | 'accent'
  | 'action'
  | 'on-light'
  | 'on-light-subtle'
  | 'on-dark';
export type ProgressIndicatorSize = 'small' | 'large';
export type ProgressIndicatorType = 'linear' | 'circular';
export type ProgressIndicatorVariant = 'indeterminate' | 'determinate';

interface CircularDimensions {
  cx: number;
  cy: number;
  r: number;
  viewBox: string;
}

@customElement('hds-progress-indicator')
export class ProgressIndicator extends LitElement {
  static styles = styles;

  @property({type: String}) color: ProgressIndicatorColor = 'accent';
  @property({type: String}) size: ProgressIndicatorSize = 'small';
  @property({type: String, attribute: 'indicator-type'}) indicatorType: ProgressIndicatorType =
    'linear';
  @property({type: String}) variant: ProgressIndicatorVariant = 'indeterminate';
  @property({type: Number, reflect: true}) progress: number = 0;
  @property({type: String, attribute: 'aria-label'}) ariaLabel;
  @query('.circularProgress') protected circularDeterminateVariant!: HTMLElement;

  private _circularProgressLength;

  firstUpdated() {
    this._circularProgressLength = this.circularDeterminateVariant?.getAttribute('r');
  }

  updated() {
    if (this.variant !== 'determinate') return;

    if (this.indicatorType === 'circular') {
      if (this.progress === 0) {
        this.circularDeterminateVariant.style.opacity = '0';
      } else if (this.progress > 0) {
        const r = this._circularProgressLength;
        const c = Math.PI * (r * 2);
        const pct = ((100 - this.progress) / 100) * c;
        if (this.circularDeterminateVariant !== null) {
          this.circularDeterminateVariant.style.opacity = '1';
          this.circularDeterminateVariant.style.strokeDashoffset = `${Math.round(pct)}`;
        }
      }
    }
  }

  // eslint-disable-next-line no-undef
  protected update(changedProperties: PropertyValues) {
    super.update(changedProperties);
    this.progress = clamp(this.progress, 0, 100);
  }

  protected render() {
    const classes = {};
    classes[this.size] = true;
    classes[this.color] = true;
    classes[this.indicatorType] = true;
    classes[this.variant] = true;
    classes['progress-0'] = this.progress === 0;

    return html`
      <div
        class="-hds-progress-indicator-wrapper ${classMap(classes)}"
        role="progressbar"
        aria-label="${this.ariaLabel || 'progress indicator'}"
        aria-live="polite"
      >
        ${this.indicatorType === 'circular'
          ? this._renderCircularVariant()
          : this._renderLinearVariant()}
      </div>
    `;
  }

  private _renderLinearVariant() {
    return html`
      <svg width="100%" height="2">
        <line class="track" x1="0" y1="50%" x2="100%" y2="50%"></line>
        <line
          class="progress"
          x1="0%"
          y1="50%"
          x2="${this.variant === 'indeterminate' ? '100%' : `${clamp(this.progress, 0, 100)}%`}"
          y2="50%"
        ></line>
      </svg>
    `;
  }

  private _renderCircularVariant() {
    const {cx, cy, r, viewBox} = this.getCircularDimensions();

    return html`
      <svg viewBox="${viewBox}" xmlns="http://www.w3.org/2000/svg">
        <circle class="path" cx="${cx}" cy="${cy}" r="${r}"></circle>
        <circle cx="${cx}" cy="${cy}" r="${r}" class="circularProgress"></circle>
      </svg>
    `;
  }

  private getCircularDimensions() {
    const dimensions: Partial<CircularDimensions> = {};
    switch (this.size) {
      case 'small':
        dimensions.cx = 7;
        dimensions.r = 6;
        break;
      case 'large':
      default:
        dimensions.cx = 15;
        dimensions.r = 14;
        break;
    }
    dimensions.cy = dimensions.cx;
    dimensions.viewBox = `0 0 ${2 * dimensions.cx} ${2 * dimensions.cy}`;
    return dimensions;
  }
}

declare global {
  interface HTMLElementTagNameMap {
    'hds-progress-indicator': ProgressIndicator;
  }
}
