import {customElement, BaseElement} from '@here/hds-base';
import '@here/hds-icon';
import {ListSelectPayload, List, ListItem} from '@here/hds-list';
import {html, LitElement, PropertyValues} from 'lit';
import {property, query} from 'lit/decorators.js';
import {classMap} from 'lit/directives/class-map.js';
import {styles} from './hds-dropdown.css.js';
import {computePosition, autoPlacement, Placement, offset} from '@floating-ui/dom';

export type DropdownVariant = 'solid' | 'subtle';
export type DropdownSize = 'small' | 'medium' | 'large';
export type DropdownPlacement =
  | 'auto'
  | 'top'
  | 'top-start'
  | 'top-end'
  | 'right'
  | 'right-start'
  | 'right-end'
  | 'bottom'
  | 'bottom-start'
  | 'bottom-end'
  | 'left'
  | 'left-start'
  | 'left-end';
export type DropdownChangePayload = {open: boolean};

/**
 * @slot trigger Element to be the trigger for the dropdown menu
 * @slot toggle Label for the toggle element
 * @slot menu Element `hds-list` for the items of the dropdown menu
 *
 * @event {CustomEvent<{open: boolean}>} change Fired when the dropdown has been changed: expanded/collapsed `DropdownChangePayload`
 * @event {CustomEvent<{id: string; index: number}>} select Fired when an item from the menu has been selected `ListSelectPayload`
 */
@customElement('hds-dropdown')
export class Dropdown extends BaseElement {
  static styles = styles;
  static shadowRootOptions = {...LitElement.shadowRootOptions, delegatesFocus: true};
  static utils = {computePosition, autoPlacement, offset};

  isTabbable = true;
  @property({type: Boolean, reflect: true}) open = false;
  @property({type: String}) placement: DropdownPlacement = 'bottom';
  @property({type: Boolean, attribute: 'hide-on-scroll'}) hideOnScroll = false;

  @property({
    type: Array,
    attribute: 'allowed-placements',
    converter: {
      fromAttribute: value => value?.split(',') as DropdownPlacement[],
    },
  })
  allowedPlacements: DropdownPlacement[] = ['top', 'right', 'bottom', 'left'];

  @query('[name="menu"]') protected menuSlot!: HTMLElement;
  @query('.menu-wrapper') protected menuWrapper!: HTMLElement;

  constructor() {
    super();
    this._handleKeydown = this._handleKeydown.bind(this);
    this._handleClickOutside = this._handleClickOutside.bind(this);
    this._handleScroll = this._handleScroll.bind(this);
  }

  connectedCallback() {
    super.connectedCallback && super.connectedCallback();
    this._addEventListeners();
  }

  disconnectedCallback() {
    super.disconnectedCallback && super.disconnectedCallback();
    this._removeEventListeners();
  }

  protected async updated(changedPropertyValues: PropertyValues) {
    if (changedPropertyValues.has('open') && changedPropertyValues.get('open') !== undefined) {
      this.dispatchEvent(
        new CustomEvent<DropdownChangePayload>('change', {
          detail: {
            open: this.open,
          },
          bubbles: true,
          composed: true,
        })
      );
    }

    if (this.open) {
      this.updatePosition();
      (this._getMenu()[0] as HTMLElement).focus();
    }
  }

  protected async updatePosition() {
    const isAuto = this.getAttribute('placement') === 'auto';
    const offsetFn = Dropdown.utils.offset(8);
    const autoPlacementFn = isAuto
      ? Dropdown.utils.autoPlacement({
          allowedPlacements: this.allowedPlacements as Placement[],
        })
      : undefined;
    const middleware = isAuto ? [autoPlacementFn, offsetFn] : [offsetFn];
    const placement = isAuto ? undefined : (this.placement as Placement);

    const pos = await Dropdown.utils.computePosition(this, this.menuWrapper, {
      placement,
      strategy: 'fixed',
      middleware,
    });

    Object.assign(this.menuWrapper.style, {
      top: `${pos.y}px`,
      left: `${pos.x}px`,
    });

    this.placement = pos.placement;
  }

  protected render() {
    const menuClasses = {
      'menu-wrapper': true,
      open: this.open,
      [`placement-${this.placement}`]: true,
    };

    return html`
      <div
        id="hds-dropdown"
        class="hds-dropdown-wrapper"
        @click="${(e: MouseEvent) => e.stopPropagation()}"
        @select="${this._handleOnSelect}"
      >
        <div class="trigger-wrapper">
          <slot name="trigger" @click="${this._handleToggleClick}"></slot>
        </div>
        <div class="${classMap(menuClasses)}">
          <slot name="menu"></slot>
        </div>
      </div>
    `;
  }

  private _handleKeydown(e: KeyboardEvent) {
    // emits close event on Escape key down
    if (e.key === 'Escape') {
      this._handleClose();
    }
  }

  private _handleClose() {
    this.open = false;

    this.querySelectorAll<ListItem>('hds-list-item[selected]').forEach(itemElement => {
      const item = itemElement;
      item.selected = false;
    });

    // TODO: review this after #1071 is closed
    this.querySelectorAll<List>('hds-list').forEach(listElement => {
      const list = listElement;
      list.selectedIndex = -1;
    });
  }

  private _handleClickOutside(e: Event) {
    const closestDropdown =
      (<HTMLElement>e.target).closest &&
      (<HTMLElement>e.target).closest(this.tagName.toLowerCase());
    if (closestDropdown !== this) {
      this._handleClose();
    }
  }

  private _removeEventListeners() {
    this.ownerDocument.removeEventListener('click', this._handleClickOutside, true);
    this.ownerDocument.removeEventListener('scroll', this._handleScroll, true);
    this.ownerDocument.removeEventListener('wheel', this._handleScroll, true);
    this.ownerDocument.removeEventListener('keydown', this._handleKeydown);
    this.ownerDocument.defaultView?.removeEventListener('resize', this._handleScroll);
  }

  private _addEventListeners() {
    this._removeEventListeners();
    this.ownerDocument.addEventListener('click', this._handleClickOutside, true);
    this.ownerDocument.addEventListener('scroll', this._handleScroll, true);
    this.ownerDocument.addEventListener('wheel', this._handleScroll, true);
    this.ownerDocument.addEventListener('keydown', this._handleKeydown);
    this.ownerDocument.defaultView?.addEventListener('resize', this._handleScroll);
  }

  private async _handleOnSelect(e: CustomEvent<ListSelectPayload>) {
    e.stopPropagation();
    await this.updateComplete;
    this.dispatchEvent(
      new CustomEvent<ListSelectPayload>('select', {
        detail: e.detail,
        bubbles: true,
        composed: true,
      })
    );

    this._handleClose();
  }

  private async _handleToggleClick() {
    this.open = !this.open;
  }

  private _getMenu() {
    return (this.menuSlot as HTMLSlotElement).assignedNodes({flatten: true});
  }

  private _handleScroll() {
    if (this.open) {
      if (this.hideOnScroll) {
        this.open = false;
        this.requestUpdate('open', true);
      } else {
        this.updatePosition();
      }
    }
  }
}

declare global {
  interface HTMLElementTagNameMap {
    'hds-dropdown': Dropdown;
  }
}
