import {IconCategory} from '@here/hds-iconlibrary';
import {useEffect, useImperativeHandle, useRef} from 'react';
import * as React from 'react';
import {Button, ButtonSelectPayload, ButtonSize, ButtonType, ButtonVariant} from '@here/hds-button';
import {HDSIcon} from '../Icon/index.js';

// eslint-disable-next-line no-new
new Button();

interface HDSButtonProperties
  extends Omit<React.HTMLAttributes<HTMLButtonElement>, 'onClick' | 'onSelect'> {
  disabled?: boolean;
  selected?: boolean;
  selectable?: boolean;
  size?: ButtonSize;
  variant?: ButtonVariant;
  width?: string;
  icon?: string;
  iconCategory?: IconCategory;
  iconRight?: string;
  iconRightCategory?: IconCategory;
  iconOnly?: boolean;
  link?: boolean;
  loading?: boolean;
  type?: ButtonType;
  onClick?: (e: MouseEvent) => void;
  onSelect?: (e: CustomEvent<ButtonSelectPayload>) => void;
}

interface HDSLinkButtonProperties extends HDSButtonProperties {
  children: React.ReactElement;
}

const HDSLinkButton = React.forwardRef((props: HDSLinkButtonProperties, ref) => {
  const {
    children,
    disabled,
    size,
    variant,
    icon,
    iconRight,
    iconCategory,
    iconRightCategory,
    iconOnly,
    ...rest
  } = props;
  // Render the button as link
  const classesMap: Record<string, boolean> = {
    'hds-button': true,
  };
  classesMap[`hds-button--size-${size ?? 'medium'}`] = true;
  classesMap[`hds-button--variant-${variant ?? 'primary'}`] = true;
  classesMap['hds-button--icon'] = !!icon || !!iconRight;
  classesMap['hds-button--icononly'] = !!iconOnly;
  classesMap['hds-button--disabled'] = !!disabled;
  const classes = Object.keys(classesMap)
    .reduce((acc, key) => {
      if (classesMap[key]) {
        acc.push(key);
      }
      return acc;
    }, [] as string[])
    .join(' ');

  const Icon = icon ? <HDSIcon name={icon} size="16px" category={iconCategory} /> : null;
  const IconRight = iconRight ? (
    <HDSIcon name={iconRight} category={iconRightCategory} size="16px" />
  ) : null;
  const linkProps = {
    ...rest,
    ...children?.props,
    link: undefined,
    // eslint-disable-next-line @typescript-eslint/no-explicit-any
    ref: ref as any,
    className: classes,
    tabIndex: disabled ? -1 : 0,
    children: (
      <>
        {Icon}
        {!iconOnly && children?.props?.children && (
          <span className="hds-button-label">{children.props.children}</span>
        )}
        {IconRight}
      </>
    ),
  };
  return React.isValidElement(children) ? (
    React.cloneElement(children, {...linkProps})
  ) : (
    <span {...linkProps} role="button">
      <>
        {Icon}
        {!iconOnly && <span className="hds-button-label">{children}</span>}
      </>
    </span>
  );
});

const HDSButton = React.forwardRef((props: HDSButtonProperties, ref) => {
  const {
    children,
    className,
    disabled,
    selected,
    selectable,
    onClick,
    onSelect,
    size,
    variant,
    width,
    icon,
    iconCategory,
    iconRight,
    iconRightCategory,
    link,
    loading,
    ...rest
  } = props;
  // Render the button as link
  if (link) {
    return (
      <HDSLinkButton {...props} {...(className && {class: className})}>
        {children as React.ReactElement}
      </HDSLinkButton>
    );
  }

  const innerRef = useRef<HTMLElement>(null);
  useImperativeHandle(ref, () => innerRef.current);

  const _handleClick = (e: MouseEvent) => {
    if (!disabled && onClick) {
      onClick(e);
    }
  };

  const _handleSelect = (e: CustomEvent<ButtonSelectPayload>) => {
    if (e.target !== innerRef.current) return;
    onSelect && onSelect(e);
  };

  useEffect(() => {
    if (innerRef.current) {
      innerRef.current.addEventListener('click', _handleClick as EventListener);
      innerRef.current.addEventListener('select', _handleSelect as EventListener);
    }
    return () => {
      innerRef.current?.removeEventListener('click', _handleClick as EventListener);
      innerRef.current?.removeEventListener('select', _handleSelect as EventListener);
    };
  }, [onClick, onSelect, innerRef]);

  return (
    <hds-button
      {...rest}
      {...(className && {class: className})}
      variant={variant}
      {...(selected && {selected: true})}
      {...(selectable && {selectable: true})}
      {...(disabled && {disabled: true})}
      {...(width && {width})}
      {...(icon && {icon})}
      {...(loading && {loading: true})}
      {...(iconCategory && {'icon-category': iconCategory})}
      {...(iconRight && {'icon-right': iconRight})}
      {...(iconRightCategory && {'icon-right-category': iconRightCategory})}
      size={size}
      ref={innerRef}
    >
      {children}
    </hds-button>
  );
});

HDSButton.defaultProps = {
  type: 'button',
  variant: 'primary',
  size: 'medium',
  iconCategory: 'core-ui',
  iconRightCategory: 'core-ui',
};
export default HDSButton;
