import {customElement} from '@here/hds-base';
import {html, LitElement} from 'lit';
import {property, query} from 'lit/decorators.js';
import {styles} from './hds-breadcrumb.css.js';
import {BreadcrumbItem} from './hds-breadcrumb-item.js';

export type BreadcrumbChangePayload = {expanded: boolean};

/**
 *
 * @event {CustomEvent<{expanded: boolean}>} change Fired when the breadcrumb status has been expanded `BreadcrumbChangePayload`
 *
 * @slot default List of `hds-breadcrumb-item` elements
 */
@customElement('hds-breadcrumb')
export class Breadcrumb extends LitElement {
  static styles = styles;

  @property({type: Boolean, attribute: 'truncate-items'}) truncateItems = false;
  @property({type: Number, attribute: 'truncate-item-buffer'}) truncateItemBuffer = 5;
  @query('slot:not([name])') protected itemsSlot!: HTMLElement;

  protected firstUpdated() {
    this._setDefaultActive();
  }

  protected render() {
    return html`
      <nav class="-hds-breadcrumb-wrapper" aria-label="breadcrumb" @expand="${this._handleExpand}">
        <ol class="breadcrumb">
          <slot></slot>
        </ol>
      </nav>
    `;
  }

  protected async _handleExpand(evt: CustomEvent<string>) {
    evt.stopPropagation();
    evt.preventDefault();
    this._getItems().forEach(item => (item.visible = true));
    await this.updateComplete;
    this.dispatchEvent(
      new CustomEvent<BreadcrumbChangePayload>('change', {
        detail: {
          expanded: true,
        },
      })
    );
  }

  protected _setDefaultActive() {
    const items = this._getItems();
    if (items.length === 0) {
      return;
    }
    if (this.truncateItems) {
      items.forEach((item, index) => {
        if (
          item &&
          items.length >= this.truncateItemBuffer &&
          index > 0 &&
          index < items.length - 1
        ) {
          // eslint-disable-next-line no-param-reassign
          item.visible = false;
        }
      });
      items[1].isEllipsis = true;
    }
    items[items.length - 1].isLastItem = true;
  }

  protected _getItems() {
    return (this.itemsSlot as HTMLSlotElement)
      .assignedNodes({flatten: true})
      .filter((e: Node) => e instanceof BreadcrumbItem) as BreadcrumbItem[];
  }
}

declare global {
  interface HTMLElementTagNameMap {
    'hds-breadcrumb': Breadcrumb;
  }
}
