import React, {useEffect, useImperativeHandle, useRef, useState} from 'react';
import {Tooltip} from '@here/hds-tooltip';
// eslint-disable-next-line no-new
new Tooltip();

interface HDSTooltipProperties extends React.HTMLAttributes<HTMLElement> {
  id?: string;
  content: React.ReactNode;
  position?: string;
  interactive?: boolean;
  trigger?: string;
  disabled?: boolean;
}

let idCounter = 1;

const HDSTooltip = React.forwardRef((props: HDSTooltipProperties, ref) => {
  const {position, children, interactive, trigger, id, content, disabled, className, ...rest} =
    props;
  const [isReady, setIsReady] = useState(false);
  const targetRef = useRef<HTMLElement>(null);
  const idRef = useRef(`hds-tooltip-${id || idCounter++}`);
  const targetClone = React.isValidElement(children)
    ? // @ts-ignore
      React.cloneElement(children, {ref: targetRef})
    : content;
  useImperativeHandle(ref, () => targetRef.current);

  useEffect(() => {
    if (targetRef.current && targetRef.current.setAttribute) {
      targetRef.current.setAttribute('data-hds-tooltip', idRef.current);
      setIsReady(true);
    }
  }, [targetRef]);

  return (
    <>
      {targetClone}
      {isReady && (
        <hds-tooltip
          id={idRef.current}
          {...(position && {position})}
          {...(className && {class: className})}
          {...(interactive && {interactive})}
          {...(trigger && {trigger})}
          ref={ref}
          {...(disabled && {disabled})}
          {...rest}
        >
          <div>{content}</div>
        </hds-tooltip>
      )}
    </>
  );
});

HDSTooltip.defaultProps = {
  id: '',
  disabled: false,
  position: 'bottom',
  interactive: false,
  trigger: 'mouseenter focus',
};

export default HDSTooltip;
