import React, {useEffect, useImperativeHandle} from 'react';
import {
  Accordion,
  AccordionChangePayload,
  AccordionVariant,
  AccordionArrowPosition,
  AccordionExpandMode,
} from '@here/hds-accordion';

// eslint-disable-next-line no-new
new Accordion();

export interface HDSAccordionProperties
  extends Omit<React.HTMLAttributes<HTMLElement>, 'onChange'> {
  disabled?: boolean;
  variant?: AccordionVariant;
  arrowPosition?: AccordionArrowPosition;
  expandMode?: AccordionExpandMode;
  defaultExpanded?: boolean;
  disableContentPadding?: boolean;
  // eslint-disable-next-line no-unused-vars
  onChange?: (value: AccordionChangePayload) => void;
}

const HDSAccordion = React.forwardRef((props: HDSAccordionProperties, ref) => {
  const {
    children,
    disabled,
    variant,
    className,
    arrowPosition,
    expandMode,
    defaultExpanded,
    disableContentPadding,
    onChange,
    ...rest
  } = props;

  const innerRef = React.useRef<HTMLElement>(null);
  useImperativeHandle(ref, () => innerRef.current);

  const _handleChange = (e: CustomEvent<AccordionChangePayload>) => {
    if (e.currentTarget !== innerRef.current) return;
    onChange && onChange(e.detail);
  };

  useEffect(() => {
    if (onChange && innerRef.current) {
      innerRef.current.addEventListener('change', _handleChange as EventListener);
    }
    return () => innerRef.current?.removeEventListener('change', _handleChange as EventListener);
  }, [onChange, innerRef]);

  return (
    <hds-accordion
      variant={variant}
      {...(className && {class: className})}
      {...(disabled && {disabled})}
      {...(arrowPosition && {'arrow-position': arrowPosition})}
      {...(expandMode && {'expand-mode': expandMode})}
      {...(defaultExpanded && {'default-expanded': defaultExpanded})}
      {...(disableContentPadding && {'disable-content-padding': disableContentPadding})}
      ref={innerRef}
      {...rest}
    >
      {children}
    </hds-accordion>
  );
});

HDSAccordion.defaultProps = {
  variant: 'default',
  arrowPosition: 'end',
  expandMode: 'many',
  defaultExpanded: false,
  disableContentPadding: false,
  disabled: false,
};
export default HDSAccordion;
