import {customElement, closestElement} from '@here/hds-base';
import {html, LitElement} from 'lit';
import {property} from 'lit/decorators.js';
import tippy from 'tippy.js';

@customElement('hds-tooltip')
export class Tooltip extends LitElement {
  static shadowRootOptions = {...LitElement.shadowRootOptions, delegatesFocus: true};
  @property({type: String}) position = 'bottom';
  @property({type: Boolean}) interactive = false;
  @property({type: String}) trigger = 'mouseenter focus';
  private isDisabled = false;
  private tooltipInstance: any;

  constructor() {
    super();
    this.hide = this.hide.bind(this);
  }

  get disabled() {
    return this.isDisabled;
  }

  @property({type: Boolean, reflect: true}) set disabled(value: boolean) {
    const oldVal = this.isDisabled;
    this.isDisabled = value;
    if (this.tooltipInstance?.length) {
      if (this.isDisabled) {
        this.tooltipInstance[0].disable();
      } else {
        this.tooltipInstance[0].enable();
      }
    }
    this.requestUpdate('disable', oldVal);
  }

  get tippyInstance(): any {
    return this.tooltipInstance;
  }

  connectedCallback() {
    super.connectedCallback();
    this.hide();
  }

  firstUpdated() {
    this.createTooltip();
  }

  disconnectedCallback() {
    super.disconnectedCallback();
    this.destroyTooltip();
  }

  hide() {
    this.destroyTooltip();
  }

  protected render() {
    return html`<span>
      <slot></slot>
    </span>`;
  }

  private createTooltip() {
    if (!this.id) {
      return;
    }
    const parent = closestElement<HTMLElement>("[data-theme^='hds-']", this) || this.parentElement!;
    // @ts-ignore
    this.tooltipInstance = tippy(`[data-hds-tooltip=${this.id}]`, {
      content: this.children[0],
      placement: this.position,
      allowHTML: true,
      interactive: this.interactive,
      maxWidth: 256,
      theme: 'hds',
      appendTo: () => parent,
      trigger: this.trigger,
      offset: [0, 15],
    });

    if (this.disabled) {
      if (this.tooltipInstance?.length) {
        this.tooltipInstance[0].disable();
      }
    }
  }

  private destroyTooltip() {
    if (this.tooltipInstance) {
      this.tooltipInstance.forEach(instance => instance.destroy());
      this.tooltipInstance = null;
    }
  }
}

declare global {
  interface HTMLElementTagNameMap {
    'hds-tooltip': Tooltip;
  }
}
