import {ListSelectPayload} from '@here/hds-list';
import React, {useEffect, useImperativeHandle} from 'react';
import {ToggleList, TogglelistExpandMode, ToggleListChangePayload} from '@here/hds-togglelist';
import {HDSListProperties} from '../List/HDSList.js';

// eslint-disable-next-line no-new
new ToggleList();

interface HDSToggleListProperties extends Omit<HDSListProperties, 'onChange'> {
  expandMode?: TogglelistExpandMode;
  defaultExpanded?: boolean;
  onChange?: (value: ToggleListChangePayload) => void;
}

const HDSToggleList = React.forwardRef((props: HDSToggleListProperties, ref) => {
  const {children, expandMode, defaultExpanded, className, onChange, onSelect, ...rest} = props;

  const innerRef = React.useRef<HTMLElement>(null);
  useImperativeHandle(ref, () => innerRef.current);

  const _handleChange = (e: CustomEvent<ToggleListChangePayload>) => {
    onChange && onChange(e.detail);
  };
  const _handleSelect = (e: CustomEvent<ListSelectPayload>) => {
    onSelect && onSelect(e.detail);
  };

  useEffect(() => {
    if (onChange && innerRef.current) {
      innerRef.current.addEventListener('change', _handleChange as EventListener);
    }
    return () => innerRef.current?.removeEventListener('change', _handleChange as EventListener);
  }, [onChange, innerRef]);

  useEffect(() => {
    if (onSelect && innerRef.current) {
      innerRef.current.addEventListener('select', _handleSelect as EventListener);
    }
    return () => innerRef.current?.removeEventListener('select', _handleSelect as EventListener);
  }, [onSelect, innerRef]);

  return (
    <hds-togglelist
      {...(expandMode && {'expand-mode': expandMode})}
      {...(defaultExpanded && {'default-expanded': defaultExpanded})}
      {...(className && {class: className})}
      ref={innerRef}
      {...rest}
    >
      {children}
    </hds-togglelist>
  );
});

HDSToggleList.defaultProps = {
  expandMode: 'many',
  defaultExpanded: false,
};

export default HDSToggleList;
