import {IconCategory} from '@here/hds-iconlibrary';
import React from 'react';
import {Tab} from '@here/hds-tabs';

// eslint-disable-next-line no-new
new Tab();

interface HDSTabProps extends React.HTMLAttributes<HTMLElement> {
  children: React.ReactNode;
  menu?: React.ReactNode;
  icon?: string;
  iconCategory?: IconCategory;
  link?: boolean;
  chevron?: boolean;
  clickAction?: boolean;
  externalLink?: boolean;
  selected?: boolean;
  disabled?: boolean;
  ariaControls?: string;
  ariaRole?: string;
  tabPanelId?: string;
}
const HDSTab = React.forwardRef(
  (
    {
      icon,
      iconCategory,
      selected,
      disabled,
      ariaControls,
      ariaRole,
      link,
      chevron,
      children,
      tabPanelId,
      className,
      clickAction,
      externalLink,
      menu,
      ...rest
    }: HDSTabProps,
    ref
  ) => (
    <hds-tab
      icon={icon}
      aria-controls={ariaControls}
      {...(ariaRole && {'aria-role': ariaRole})}
      {...(tabPanelId && {'tab-panel-id': tabPanelId})}
      {...(selected && {selected})}
      {...(disabled && {disabled})}
      {...(className && {class: className})}
      {...(link && {link})}
      {...(chevron && {chevron})}
      {...(clickAction && {'click-action': clickAction})}
      {...(externalLink && {'external-link': externalLink})}
      {...(iconCategory && {'icon-category': iconCategory})}
      ref={ref}
      {...rest}
    >
      {children}
      {menu && <div slot="menu">{menu}</div>}
    </hds-tab>
  )
);

HDSTab.defaultProps = {
  icon: '',
  iconCategory: 'core-ui',
  link: false,
  clickAction: false,
  externalLink: false,
  disabled: false,
  ariaControls: undefined,
  ariaRole: 'tab',
  tabPanelId: '',
  selected: false,
};

export default HDSTab;
