import {customElement} from '@here/hds-base';
import '@here/hds-icon';
import {IconCategory} from '@here/hds-iconlibrary';
import '@here/hds-progress-indicator';
import {html, LitElement, nothing, TemplateResult} from 'lit';
import {property, query} from 'lit/decorators.js';
import {classMap} from 'lit/directives/class-map.js';
import {ifDefined} from 'lit/directives/if-defined.js';
import {styles} from './hds-dropzone.css.js';

export type DropzoneOrientation = 'horizontal' | 'vertical';
export type DropzoneChangePayload = {files: FileList | null};

/**
 * @event {CustomEvent<{files: FileList | null}>} change Fired when there has been a change in the filelist `DropzoneChangePayload`
 */
@customElement('hds-dropzone')
export class Dropzone extends LitElement {
  static styles = styles;

  @property({type: Boolean, reflect: true}) progress = false;
  @property({type: Boolean, attribute: false}) dragging = false;
  @property({type: Boolean, reflect: true}) error = false;
  @property({type: Boolean, reflect: true}) disabled = false;
  @property({type: Boolean}) multiple = false;
  @property({type: String}) accept = '';
  @property({type: Object, attribute: false}) files: FileList | null = null;
  @property({type: String}) icon = '';
  @property({type: String, attribute: 'icon-category'}) iconCategory: IconCategory = 'core-ui';
  @property({type: String}) orientation: DropzoneOrientation = 'horizontal';
  @property({type: String, attribute: 'text-dropzone-browse-files'}) textDropzoneBrowseFiles =
    'Browse files';
  @property({type: String, attribute: 'text-dropzone-drop-files'}) textDropzoneDropFiles =
    'Drop files here';
  @property({type: String, attribute: 'text-dropzone-or'}) textDropzoneOr = 'or';
  @property({type: String, attribute: 'text-dropzone-uploading'}) textDropzoneUploading =
    'Uploading';
  @property({type: String, attribute: 'text-dropzone-dragging-over'}) textDropzoneDraggingOver = '';
  @property({type: String, attribute: 'text-dropzone-file-restrictions'})
  textDropzoneFileRestrictions = '';
  @property({type: String, attribute: 'text-assistive'}) textAssistive = '';
  @property({type: String, attribute: 'text-error'}) textError = '';
  @property({type: String, attribute: 'text-title'}) textTitle = '';
  @property({type: String, attribute: 'text-file-restrictions'}) textFileRestrictions = '';

  @query('input[type=file]') protected nativeInput!: HTMLInputElement;

  /**
   * Asynchronous function that clears all selected/dragged files
   */
  async reset() {
    this.nativeInput.value = '';
    await this._emitChange(this.nativeInput!.files);
  }

  browse() {
    this.nativeInput.click();
  }

  protected render() {
    const classes = {
      disabled: this.disabled,
      progress: this.progress,
      error: this.error,
      'dragging-over': this.dragging,
    };
    classes[`orientation-${this.orientation}`] = true;

    const isDraggingOver =
      this.dragging && this.textDropzoneDraggingOver && this.textDropzoneDraggingOver.length;

    let dropzoneContent: TemplateResult | symbol = nothing;
    if (this.progress) dropzoneContent = this.renderProgress();
    else if (isDraggingOver) dropzoneContent = this.renderDraggingOver();
    else dropzoneContent = this.renderDropzone();

    return html`
      <div
        class="-hds-dropzone-wrapper ${classMap(classes)}"
        @drop="${this._onDrop}"
        @dragover="${this._onDragOver}"
        @dragleave="${this._onDragLeave}"
      >
        <div class="header">
          <span> ${ifDefined(this.textTitle)} </span>
          <span class="caption"> ${ifDefined(this.textFileRestrictions)} </span>
        </div>
        <div class="dropzone">${dropzoneContent}</div>
        <div class="footer">
          <span class="caption">
            ${ifDefined(this.error && this.textError ? this.textError : this.textAssistive)}
          </span>
        </div>
        <input
          type="file"
          accept="${this.accept}"
          ?multiple="${this.multiple}"
          @change="${this._onNativeInputChange}"
        />
      </div>
    `;
  }

  private async _emitChange(files: FileList | null) {
    if (!this.progress) {
      this.files = files;
      await this.updateComplete;
      this.dispatchEvent(
        new CustomEvent<DropzoneChangePayload>('change', {
          detail: {files},
          bubbles: true,
          composed: true,
        })
      );
    }
  }

  private async _onNativeInputChange(e: InputEvent) {
    e.stopPropagation();
    e.preventDefault();
    await this._emitChange(this.nativeInput!.files);
  }

  private async _onDrop(e: InputEvent) {
    this.dragging = false;
    e.stopPropagation();
    e.preventDefault();
    await this._emitChange(e.dataTransfer!.files);
  }

  private _onDragOver(e: MouseEvent) {
    this.dragging = true;
    e.stopPropagation();
    e.preventDefault();
  }

  private _onDragLeave(e: MouseEvent) {
    this.dragging = false;
    e.stopPropagation();
    e.preventDefault();
  }

  private renderProgress() {
    return html` <div class="row uploading">
      <hds-progress-indicator
        class="progress-indicator"
        size="large"
        color="action"
        indicator-type="circular"
      ></hds-progress-indicator>
      <div>${this.textDropzoneUploading}</div>
    </div>`;
  }

  private renderDraggingOver() {
    return html` <div class="row">${this.textDropzoneDraggingOver}</div>`;
  }

  private renderIcon() {
    return this.icon
      ? html` <div class="row uploading">
          <hds-icon
            class="icon"
            size="16px"
            name="${this.icon}"
            category="${this.iconCategory}"
          ></hds-icon>
        </div>`
      : nothing;
  }

  private renderSeparator() {
    return this.orientation === 'vertical' ? html` <div class="row"></div>` : nothing;
  }

  private renderDropzone() {
    const textDropzoneBrowseFiles =
      this.textDropzoneBrowseFiles.trim() !== '' ? this.textDropzoneBrowseFiles : 'Browse files';

    return html`
      ${this.renderIcon()}
      <div class="row">
        <span>${this.textDropzoneDropFiles} ${this.renderSeparator()} ${this.textDropzoneOr}</span>
        <span>${this.renderSeparator()}</span>
        <hds-button
          @click="${this.browse}"
          ?disabled="${this.disabled}"
          class="button"
          variant="link"
          size="small"
          icon-right="folder-open"
          icon-category="core-ui"
          >${textDropzoneBrowseFiles}</hds-button
        >
      </div>
      <div class="row dropzone-file-restrictions">${this.textDropzoneFileRestrictions}</div>
    `;
  }
}

declare global {
  interface HTMLElementTagNameMap {
    'hds-dropzone': Dropzone;
  }
}
