import React, {useRef, useEffect, useImperativeHandle} from 'react';
import {
  Stepper,
  StepperCurrentActiveStepPayload,
  StepperOrientation,
  StepStatePayload,
} from '@here/hds-stepper';

// eslint-disable-next-line no-new
new Stepper();

interface HDSStepperProperties extends React.HTMLAttributes<HTMLElement> {
  nonLinear?: boolean;
  hideLabel?: boolean;
  bottomLabel?: boolean;
  orientation?: StepperOrientation;
  activeStep?: number;
  completeStep?: number;
  errorStep?: number;
  children: React.ReactNode;
  onStepStateChange?: (stepState: StepStatePayload) => void;
  getCurrentActiveStep?: (currentActiveStep: StepperCurrentActiveStepPayload) => void;
}

const HDSStepper = React.forwardRef((props: HDSStepperProperties, ref) => {
  const {
    children,
    nonLinear,
    hideLabel,
    activeStep,
    completeStep,
    className,
    errorStep,
    onStepStateChange,
    getCurrentActiveStep,
    bottomLabel,
    orientation,
    ...rest
  } = props;
  const innerRef = useRef<HTMLElement>(null);
  useImperativeHandle(ref, () => innerRef.current);

  const _handleStepStateChange = (e: CustomEvent<StepStatePayload>) => {
    onStepStateChange && onStepStateChange(e.detail);
  };

  const _handleCurrentActiveStepChange = (e: CustomEvent<StepperCurrentActiveStepPayload>) => {
    if (e.target !== innerRef.current) return;
    getCurrentActiveStep && getCurrentActiveStep(e.detail);
  };

  useEffect(() => {
    if (innerRef.current) {
      if (onStepStateChange) {
        innerRef.current.addEventListener('state', _handleStepStateChange as EventListener);
      }
    }
    return () => {
      innerRef.current?.removeEventListener('state', _handleStepStateChange as EventListener);
    };
  }, [onStepStateChange, innerRef]);

  useEffect(() => {
    if (innerRef.current) {
      if (getCurrentActiveStep) {
        innerRef.current.addEventListener(
          'current-active-step',
          _handleCurrentActiveStepChange as EventListener
        );
      }
    }
    return () => {
      innerRef.current?.removeEventListener(
        'current-active-step',
        _handleCurrentActiveStepChange as EventListener
      );
    };
  }, [getCurrentActiveStep, innerRef]);

  return (
    <hds-stepper
      {...(nonLinear && {'non-linear': nonLinear})}
      {...(bottomLabel && {'bottom-label': bottomLabel})}
      {...(hideLabel && {'hide-label': hideLabel})}
      {...(className && {class: className})}
      active-step={activeStep}
      {...((completeStep || completeStep === 0) && {
        'complete-step': completeStep,
      })}
      {...((errorStep || errorStep === 0) && {'error-step': errorStep})}
      orientation={orientation}
      ref={innerRef}
      {...rest}
    >
      {children}
    </hds-stepper>
  );
});

HDSStepper.defaultProps = {
  orientation: 'horizontal',
  nonLinear: false,
  hideLabel: false,
  bottomLabel: false,
  activeStep: 0,
};

export default HDSStepper;
