import {IconCategory} from '@here/hds-iconlibrary';
import * as React from 'react';
import {Slider, SliderType, SliderSize, SliderChangePayload} from '@here/hds-slider';
import {useEffect, useImperativeHandle, useRef} from 'react';
import {IconStyle} from 'hds-icon/build/hds-icon.js';

// eslint-disable-next-line no-new
new Slider();

interface HDSSliderProperties extends Omit<React.HTMLAttributes<HTMLElement>, 'onChange'> {
  disabled?: boolean;
  min?: number;
  max?: number;
  step?: number;
  type?: SliderType;
  size?: SliderSize;
  textStart?: string;
  textEnd?: string;
  trackFillStart?: number;
  noTrackFill?: boolean;
  trackColors?: string[];
  trackColorsLengths?: string[];
  stepValues?: number[];
  stepLabels?: string[];
  iconStart?: string;
  iconStartStyle?: IconStyle;
  iconStartCategory?: IconCategory;
  iconEnd?: string;
  iconEndStyle?: IconStyle;
  iconEndCategory?: IconCategory;
  value?: number;
  value2?: number;
  onChange?: (_: CustomEvent<SliderChangePayload>) => void;
}

const HDSSlider = React.forwardRef((props: HDSSliderProperties, ref) => {
  const {
    disabled,
    min,
    max,
    step,
    type,
    size,
    textStart,
    textEnd,
    trackFillStart,
    noTrackFill,
    trackColors,
    trackColorsLengths,
    stepValues,
    stepLabels,
    iconStart,
    iconStartStyle,
    iconStartCategory,
    iconEnd,
    iconEndStyle,
    iconEndCategory,
    value,
    value2,
    className,
    onChange,
    ...rest
  } = props;

  const innerRef = useRef<HTMLElement>(null);
  useImperativeHandle(ref, () => innerRef.current);

  const _handleChange = (e: CustomEvent<SliderChangePayload>) => {
    if (e.target !== innerRef.current) return;
    onChange && onChange(e);
  };

  useEffect(() => {
    if (innerRef.current) {
      innerRef.current.addEventListener('change', _handleChange as EventListener);
    }
    return () => {
      innerRef.current?.removeEventListener('change', _handleChange as EventListener);
    };
  }, [onChange, innerRef]);

  return (
    <hds-slider
      ref={innerRef}
      {...(className && {class: className})}
      min={min}
      max={max}
      step={step}
      type={type}
      size={size}
      text-start={textStart}
      text-end={textEnd}
      track-fill-start={trackFillStart}
      {...(noTrackFill && {'no-track-fill': true})}
      track-colors={trackColors}
      track-colors-lengths={trackColorsLengths}
      step-values={stepValues}
      step-labels={stepLabels}
      icon-start={iconStart}
      icon-start-style={iconStartStyle}
      icon-start-category={iconStartCategory}
      icon-end={iconEnd}
      icon-end-style={iconEndStyle}
      icon-end-category={iconEndCategory}
      value={value}
      value2={value2}
      {...(disabled && {disabled: true})}
      {...rest}
    ></hds-slider>
  );
});

HDSSlider.displayName = 'HDSSlider';
HDSSlider.defaultProps = {
  disabled: false,
  min: 0,
  max: 100,
  step: 0,
  type: 'default',
  size: 'medium',
};
export default HDSSlider;
