import {customElement} from '@here/hds-base';
import {html, LitElement} from 'lit';
import {property, query} from 'lit/decorators.js';
import {styles} from './hds-accordion.css.js';
import {AccordionItem} from './hds-accordion-item.js';

export type AccordionVariant = 'default' | 'dense' | 'solid';
export type AccordionArrowPosition = 'start' | 'end';
export type AccordionExpandMode = 'one' | 'many';
export type AccordionChangePayload = {index: number; id: string; expanded: boolean};

/**
 * @slot default Slot for `hds-accordion-item` elements
 *
 * @event {CustomEvent<{index: number; id: string; expanded: boolean}>} change Fired when an accordion item has expanded/collapsed `AccordionChangePayload`
 */
@customElement('hds-accordion')
export class Accordion extends LitElement {
  static styles = styles;
  static shadowRootOptions = {...LitElement.shadowRootOptions, delegatesFocus: true};
  @property({type: String, reflect: true}) variant: AccordionVariant = 'default';
  @property({type: String, reflect: true, attribute: 'arrow-position'})
  arrowPosition: AccordionArrowPosition = 'end';
  @property({type: String, reflect: true, attribute: 'expand-mode'})
  expandMode: AccordionExpandMode = 'many';
  @property({type: Boolean, reflect: true, attribute: 'default-expanded'}) defaultExpanded = false;
  @property({type: Boolean, attribute: 'disable-content-padding'}) disableContentPadding = false;
  @property({type: Boolean, reflect: true}) disabled = false;
  @query('slot') protected slotElement!: HTMLSlotElement;
  private _accordionAssignedElementsCache: Element[] = [];

  protected updated() {
    this._updateAccordionItems();
  }

  protected render() {
    return html`
      <div
        class="-hds-accordion-wrapper"
        @keydown="${this._onKeyDown}"
        @slotchange="${this._onSlotChange}"
        @change="${this._onChange}"
      >
        <slot></slot>
      </div>
    `;
  }

  private _onSlotChange() {
    this._accordionAssignedElementsCache = this.slotElement.assignedElements();

    if (this.defaultExpanded) {
      const items = this._getItems();
      for (let i = 0; i < items.length; i += 1) {
        if ((this.expandMode === 'one' && i === 0) || this.expandMode === 'many') {
          items[i].expanded = true;
        }
      }
    }
    this._updateAccordionItems();
  }

  private _getFocusedItem() {
    return this._getItems().find(item => item === document.activeElement);
  }

  private _focusItemByIndex(index: number) {
    this._getItems()[index]?.focus();
  }

  private _onKeyDown(e: KeyboardEvent) {
    if (['ArrowDown', 'ArrowUp', 'Home', 'End', ' ', 'Enter'].includes(e.key)) {
      const focusedItem = this._getFocusedItem();
      const itemsCount = this._getItems().length;

      if (focusedItem !== undefined && itemsCount > 1) {
        e.preventDefault();

        if (e.key === 'ArrowUp') {
          this._focusItemByIndex(focusedItem.index === 0 ? itemsCount - 1 : focusedItem.index - 1);
        } else if (e.key === 'ArrowDown') {
          this._focusItemByIndex(focusedItem.index === itemsCount - 1 ? 0 : focusedItem.index + 1);
        } else if (e.key === 'Home') {
          this._focusItemByIndex(0);
        } else if (e.key === 'End') {
          this._focusItemByIndex(itemsCount - 1);
        } else if (e.key === ' ' || e.key === 'Enter') {
          focusedItem.expanded = !focusedItem.expanded;
        }
      }
    }
  }

  private _onChange(e: CustomEvent<AccordionChangePayload>) {
    if (this.expandMode === 'one' && e.detail.expanded) {
      this._getItems().forEach(item => {
        // eslint-disable-next-line no-param-reassign
        if (item !== e.target) item.expanded = false;
      });
    }
  }

  private _updateAccordionItems() {
    const items = this._getItems();
    for (let i = 0; i < items.length; i += 1) {
      items[i].variant = this.variant;
      items[i].arrowPosition = this.arrowPosition;
      items[i].parentDisabled = this.disabled;
      items[i].index = i;
      items[i].isFirstChild = i === 0;
      items[i].isLastChild = i === items.length - 1;
      items[i].disableContentPadding = this.disableContentPadding;
    }
  }

  private _getItems() {
    return this._accordionAssignedElementsCache.filter(
      e => e instanceof AccordionItem
    ) as AccordionItem[];
  }
}

declare global {
  interface HTMLElementTagNameMap {
    'hds-accordion': Accordion;
  }
}
