import {IconCategory} from '@here/hds-iconlibrary';
import React, {useEffect, useImperativeHandle, useRef} from 'react';
import {SwitcherTab, SwitcherTabSelectPayload} from '@here/hds-switcher';

new SwitcherTab();

interface HDSSwitcherTabProps extends Omit<React.HTMLAttributes<HTMLElement>, 'onSelect'> {
  children?: React.ReactNode;
  icon?: string;
  iconCategory?: IconCategory;
  iconRight?: string;
  iconRightCategory?: IconCategory;
  selected?: boolean;
  disabled?: boolean;
  ariaLabel?: string;
  onSelect?: (tabIndex: SwitcherTabSelectPayload) => void;
}
const HDSSwitcherTab = React.forwardRef((props: HDSSwitcherTabProps, ref) => {
  const {
    icon,
    iconCategory,
    iconRight,
    iconRightCategory,
    selected,
    disabled,
    children,
    ariaLabel,
    className,
    onSelect,
    ...rest
  } = props;
  const innerRef = useRef<HTMLElement>(null);

  useImperativeHandle(ref, () => innerRef.current);

  const _handleSelected = (e: CustomEvent<SwitcherTabSelectPayload>) => {
    if (e.target !== innerRef.current) return;
    onSelect && onSelect(e.detail);
  };

  useEffect(() => {
    if (innerRef.current) {
      if (onSelect) {
        innerRef.current.addEventListener('select', _handleSelected as EventListener);
      }
    }
    return () => {
      innerRef.current?.removeEventListener('select', _handleSelected as EventListener);
    };
  }, [onSelect, innerRef]);
  return (
    <hds-switcher-tab
      aria-label={ariaLabel}
      icon={icon}
      {...(iconCategory && {'icon-category': iconCategory})}
      iconRight={iconRight}
      {...(iconRightCategory && {'icon-right-category': iconRightCategory})}
      {...(selected && {selected})}
      {...(disabled && {disabled})}
      {...(className && {class: className})}
      ref={innerRef}
      {...rest}
    >
      {children}
    </hds-switcher-tab>
  );
});

HDSSwitcherTab.defaultProps = {
  disabled: false,
  selected: false,
  icon: undefined,
  iconCategory: 'core-ui',
  iconRight: undefined,
  iconRightCategory: 'core-ui',
  ariaLabel: undefined,
};

export default HDSSwitcherTab;
