import React, {useEffect, useImperativeHandle, useRef} from 'react';
import {Radio, RadioChangePayload} from '@here/hds-radio';

new Radio();

interface HDSRadioProps extends Omit<React.HTMLAttributes<HTMLElement>, 'onChange'> {
  children: React.ReactNode;
  checked?: boolean;
  defaultChecked?: boolean;
  disabled?: boolean;
  size?: string;
  id?: string;
  name?: string;
  value?: string;
  description?: React.ReactNode;
  autocomplete?: string;
  onChange?: (event: CustomEvent<RadioChangePayload>) => void;
}

const HDSRadio = React.forwardRef((props: HDSRadioProps, ref) => {
  const {
    checked,
    defaultChecked,
    disabled,
    id,
    name,
    value,
    size,
    description,
    className,
    children,
    onChange,
    autocomplete,
    ...rest
  } = props;
  const innerRef = useRef<HTMLElement>(null);
  useImperativeHandle(ref, () => innerRef.current);

  const _handleChange = (e: CustomEvent<RadioChangePayload>) => {
    if (e.target !== innerRef.current) return;
    onChange && onChange(e);
  };
  useEffect(() => {
    if (innerRef.current && onChange) {
      innerRef.current.addEventListener('change', _handleChange as EventListener);
    }
    return () => {
      innerRef.current?.removeEventListener('change', _handleChange as EventListener);
    };
  }, [onChange, innerRef]);

  return (
    <hds-radio
      {...(checked && {checked})}
      {...(defaultChecked && {defaultChecked})}
      {...(disabled && {disabled})}
      {...(className && {class: className})}
      {...(autocomplete && {autocomplete})}
      {...(size && {size})}
      id={id}
      name={name}
      value={value}
      ref={innerRef}
      {...rest}
    >
      {children}
      {description && <span slot="description">{description}</span>}
    </hds-radio>
  );
});

HDSRadio.defaultProps = {
  checked: false,
  disabled: false,
  size: 'medium',
  value: '',
  name: '',
  description: '',
  autocomplete: '',
};

export default HDSRadio;
