import {ModalChangePayload} from '@here/hds-modal';
import React, {createContext, useCallback, useMemo, useState} from 'react';
import HDSModal, {HDSModalProperties} from './HDSModal.js';
import HDSModalContainer from './HDSModalContainer.js';

export interface ModalWrapperProps extends HDSModalProperties {
  modalId?: string;
  body?: React.ReactNode;
}

const HDSModalWrapper: React.FC<ModalWrapperProps> = props => {
  const {onClose} = props;
  const handleClose = (e: ModalChangePayload) => {
    if (onClose) {
      onClose(e);
    }
  };
  return (
    <HDSModal {...props} onClose={handleClose}>
      {props.children}
    </HDSModal>
  );
};

interface ModalContextType {
  // eslint-disable-next-line no-unused-vars
  addModal: (modal: ModalWrapperProps) => void;
}

const ModalContext = createContext({} as ModalContextType);

export default ModalContext;

let modalCounter = 1;

export const HDSModalContextProvider: React.FC<{children: React.ReactNode}> = ({children}) => {
  const [modal, setModal] = useState([] as ModalWrapperProps[]);

  const addModal = useCallback(
    (modalElement: ModalWrapperProps) => {
      modalElement.modalId = `hds-modal-${modalCounter++}`;
      setModal(currValue => [...currValue, modalElement]);
    },
    [setModal]
  );

  const contextValue = useMemo(() => ({addModal}), [addModal]);

  return (
    <ModalContext.Provider value={contextValue}>
      {children}
      <HDSModalContainer>
        {modal.map(modalEl => (
          <HDSModalWrapper {...modalEl} key={modalEl.modalId}>
            {modalEl.body}
          </HDSModalWrapper>
        ))}
      </HDSModalContainer>
    </ModalContext.Provider>
  );
};
