import {NotificationChangePayload, NotificationPosition} from '@here/hds-notification';
import React, {createContext, useCallback, useMemo, useState} from 'react';
import HDSToastContainer from './HDSToastContainer.js';
import HDSNotification, {HDSNotificationBaseProperties} from './HDSNotification.js';

let toastsCounter = 1;

export interface ToastWrapperProps extends HDSNotificationBaseProperties {
  toastId?: string;
  body?: React.ReactNode;
  id?: string;
  // eslint-disable-next-line no-unused-vars
  removeToast?: (toastId: string) => void;
}

interface ToastContextType {
  // eslint-disable-next-line no-unused-vars
  addToast: (toast: ToastWrapperProps) => void;
}

const ToastContext = createContext({} as ToastContextType);

export default ToastContext;

const HDSNotificationWrapper = (props: ToastWrapperProps) => {
  const {onClose, toastId, removeToast, children} = props;
  const handleClose = (e: NotificationChangePayload) => {
    if (onClose) {
      onClose(e);
    }
    removeToast!(toastId!);
  };
  return (
    <HDSNotification {...props} onClose={handleClose}>
      {children}
    </HDSNotification>
  );
};
export interface HDSToastContextProviderProps {
  position?: NotificationPosition;
  children?: React.ReactNode;
}

export const HDSToastContextProvider = ({position, children}: HDSToastContextProviderProps) => {
  const [toasts, setToasts] = useState([] as ToastWrapperProps[]);

  const addToast = useCallback(
    (toastElement: ToastWrapperProps) => {
      const id = `hds-toast-${toastsCounter++}`;
      toastElement.id = id;
      toastElement.toastId = id;
      setToasts(currValue => [...currValue, toastElement]);
    },
    [setToasts]
  );

  const removeToast = (toastId: string) => {
    setToasts(currValue => currValue.filter(toast => toast.toastId !== toastId));
  };

  const contextValue = useMemo(() => ({addToast}), [addToast]);

  return (
    <ToastContext.Provider value={contextValue}>
      {children}
      <HDSToastContainer position={position}>
        {toasts.map(toast => (
          <HDSNotificationWrapper
            {...toast}
            toastId={toast.toastId}
            id={toast.id}
            key={toast.toastId}
            removeToast={removeToast}
          >
            {toast.body}
          </HDSNotificationWrapper>
        ))}
      </HDSToastContainer>
    </ToastContext.Provider>
  );
};

HDSToastContextProvider.defaultProps = {
  position: 'bottom-right',
};
