import {customElement, isSlotEmpty} from '@here/hds-base';
import '@here/hds-icon';
import {IconCategory} from '@here/hds-iconlibrary';
import '@here/hds-tooltip';
import {html, LitElement, nothing, PropertyValues} from 'lit';
import {property, query, queryAssignedNodes, state} from 'lit/decorators.js';
import {classMap} from 'lit/directives/class-map.js';
import {styles} from './hds-flag.css.js';

export type FlagVariant =
  | 'subtle'
  | 'warning'
  | 'success'
  | 'error'
  | 'custom'
  | 'subtle-quiet'
  | 'warning-quiet'
  | 'success-quiet'
  | 'error-quiet'
  | 'custom-quiet';
export type FlagSize = 'small' | 'medium' | 'large';

/**
 * @slot default Content for the Flag label
 *
 * @cssprop --hds-flag-custom-accent-color Accent color value
 * @cssprop --hds-flag-custom-font-color Font color value
 * @cssprop --hds-icon-color Icon color value
 */
@customElement('hds-flag')
export class Flag extends LitElement {
  static flagIdCounter = 0;

  static styles = styles;

  @property({type: String}) variant: FlagVariant = 'subtle';
  @property({type: String}) size: FlagSize = 'small';
  @property({type: String}) icon?: string;
  @property({type: String, attribute: 'icon-category'}) iconCategory: IconCategory = 'core-ui';
  @query('slot') protected slotElement!: HTMLSlotElement;
  @query('.hds-flag-label-wrapper') protected labelEl!: HTMLElement;
  @queryAssignedNodes('', true) protected slotItemsList!: HTMLSlotElement;
  @state() private iconOnly = false;

  connectedCallback() {
    super.connectedCallback();
    this.id = this.id || `hds-flag-${++Flag.flagIdCounter}`;
  }

  protected updated(_changedProperties: PropertyValues) {
    super.updated(_changedProperties);
    if (this.icon) {
      this.iconOnly = isSlotEmpty(this.slotItemsList);
    }
  }

  protected renderIcon() {
    if (!this.icon) {
      return nothing;
    }
    const iconSize = this.size === 'small' ? '8px' : '16px';
    return html` <hds-icon
      name="${this.icon}"
      category="${this.iconCategory}"
      size="${iconSize}"
    ></hds-icon>`;
  }

  protected render() {
    const classes = {
      [`--size-${this.size}`]: true,
      [`--variant-${this.variant}`]: true,
      '--icon-only': this.iconOnly,
    };
    return html` <div class="hds-flag-wrapper ${classMap(classes)}">
      ${this.renderIcon()}
      <div class="hds-flag-label-wrapper">
        <slot></slot>
      </div>
    </div>`;
  }
}

declare global {
  interface HTMLElementTagNameMap {
    'hds-flag': Flag;
  }
}
