import {customElement, BaseElement} from '@here/hds-base';
import '@here/hds-label';
import {html, LitElement, nothing, PropertyValues} from 'lit';
import {property, query} from 'lit/decorators.js';
import {classMap} from 'lit/directives/class-map.js';
import {ifDefined} from 'lit/directives/if-defined.js';
import {styleMap} from 'lit/directives/style-map.js';
import {styles} from './hds-textarea.css.js';

export type TextareaStyleResize = 'none' | 'both' | 'horizontal' | 'vertical';
export type TextareaInputPayload = {value: string};
export type TextareaChangePayload = {value: string};

/**
 *
 * @event {CustomEvent<{value: string}>} input Fired when the value of the element has changed `TextareaInputPayload`
 * @event {CustomEvent<{value: string}>} change Fired when an alteration to the element's value is committed by the user `TextChangePayload`
 * @event {CustomEvent<Textarea>} focus Fired when the element has focus
 * @event {CustomEvent<Textarea>} blur Fired when the element loses focus
 * @event {KeyboardEvent} keydown Fired when a key is pressed
 * @event {KeyboardEvent} keyup Fired when a key is released
 *
 */
@customElement('hds-textarea')
export class Textarea extends BaseElement {
  static styles = styles;
  static idCounter = 0;
  static shadowRootOptions = {...LitElement.shadowRootOptions, delegatesFocus: true};
  static formAssociated = true;
  isTabbable = true;

  @property({type: String}) name = '';
  @property({type: String}) placeholder?;
  @property({type: String}) label?;
  @property({type: String, attribute: 'secondary-label'}) secondaryLabel?;
  @property({type: Number}) rows = -1;
  @property({type: Number}) cols = -1;
  @property({type: Boolean}) disabled = false;
  @property({type: Boolean}) readonly = false;
  @property({type: Boolean}) required = false;
  @property({type: Boolean}) autofocus = false;
  @property({type: Number}) minlength = -1;
  @property({type: Number}) maxlength = -1;
  @property({type: Boolean, attribute: 'char-counter'}) charCounter = false;
  @property({type: Boolean}) error = false;
  @property({type: String, attribute: 'error-text'}) errorText?;
  @property({type: String, attribute: 'aria-labelledby'}) ariaLabelledBy?;
  @property({type: String, reflect: true, attribute: true}) resize: TextareaStyleResize = 'none';
  @property({type: String}) autocomplete = '';
  @query('textarea') textareaElement!: HTMLTextAreaElement;
  private _labelId: string | undefined;
  private _value: string = '';

  protected get charCounterVisible(): boolean {
    return this.charCounter && this.maxlength !== -1;
  }

  protected get errorTextVisible(): boolean {
    return this.error && !!this.errorText;
  }

  connectedCallback() {
    super.connectedCallback && super.connectedCallback();
    Textarea.idCounter += 1;
    if (this.label) {
      this._labelId = `hds-textarea-label-${Textarea.idCounter}`;
    }
  }

  protected firstUpdated() {
    this.autofocus && this.focus();
  }

  protected updated(_changedProps: PropertyValues) {
    if (_changedProps.has('value') && _changedProps.get('value') !== undefined) {
      this.textareaElement.value = this.value;
      this.dispatchEvent(
        new CustomEvent<TextareaInputPayload>('input', {
          detail: {value: this.value},
          bubbles: true,
          composed: true,
        })
      );
    }
  }

  @property({type: String, reflect: true, attribute: 'value'})
  get value() {
    return this._value;
  }

  set value(val: string) {
    const oldValue = this._value;
    this._value = val;
    this.requestUpdate('value', oldValue);
  }

  setSelectionRange(start: number, end: number, direction?: 'forward' | 'backward' | 'none') {
    this.textareaElement!.setSelectionRange(start, end, direction);
  }

  setRangeText(replacement: string, start?: number, end?: number, selectionMode?: SelectionMode) {
    if (start && end) {
      this.textareaElement.setRangeText(replacement, start, end, selectionMode);
    } else {
      this.textareaElement.setRangeText(replacement);
    }
    this.value = this.textareaElement.value;
  }

  checkValidity() {
    return this.textareaElement.checkValidity();
  }

  reportValidity() {
    return this.textareaElement.reportValidity();
  }

  select() {
    this.textareaElement.select();
  }

  setCustomValidity(error: string) {
    this.textareaElement.setCustomValidity(error);
  }

  focus() {
    this.textareaElement.focus();
  }

  blur() {
    this.textareaElement.blur();
  }

  async clear() {
    this.value = '';
    await this.updateComplete;

    this.dispatchEvent(
      new CustomEvent<TextareaChangePayload>('change', {
        detail: {value: this.value},
        bubbles: true,
        composed: true,
      })
    );
  }

  private async _nativeFocus(e: Event) {
    e.stopPropagation();
    e.preventDefault();
    await this.updateComplete;
    this.dispatchEvent(
      new CustomEvent<Textarea>('focus', {
        detail: this,
        bubbles: true,
        composed: true,
      })
    );
  }

  private async _nativeBlur(e: Event) {
    e.stopPropagation();
    e.preventDefault();
    await this.updateComplete;
    this.dispatchEvent(
      new CustomEvent<Textarea>('blur', {
        detail: this,
        bubbles: true,
        composed: true,
      })
    );
  }

  protected renderCharCounter() {
    const length = Math.min(this.value.length, this.maxlength);
    return this.charCounterVisible
      ? html` <span slot="secondary">${length}/${this.maxlength}</span>`
      : nothing;
  }

  protected renderSecondaryLabel() {
    return this.secondaryLabel
      ? html` <span slot="secondary">${this.secondaryLabel}</span>`
      : nothing;
  }

  protected renderLabels() {
    if (this.label || this.secondaryLabel) {
      return html` <hds-label>${this.label || nothing} ${this.renderSecondaryLabel()}</hds-label>`;
    }
    return nothing;
  }

  protected renderHelperText() {
    if (!this.charCounterVisible && !this.errorTextVisible) {
      return undefined;
    }
    return html`
      <hds-label type="assistive" variant="error"
        >${this.errorTextVisible ? this.errorText : nothing} ${this.renderCharCounter()}
      </hds-label>
    `;
  }

  protected render() {
    const classes = {
      disabled: this.disabled,
      readonly: this.readonly,
      required: this.required,
      error: this.error,
    };

    const localStyles = {
      resize: this.resize,
    };

    return html`
      <div class="wrapper">
        ${this.renderLabels()}
        <textarea
          .value="${this.value}"
          autocomplete="${ifDefined(this.autocomplete || undefined)}"
          id="${ifDefined(this.id === '' ? undefined : this.id)}"
          name="${ifDefined(this.name === '' ? undefined : this.name)}"
          placeholder="${ifDefined(this.placeholder === '' ? undefined : this.placeholder)}"
          ?required="${this.required}"
          ?disabled="${this.disabled}"
          ?readonly="${this.readonly}"
          rows="${ifDefined(this.rows === -1 ? undefined : this.rows)}"
          cols="${ifDefined(this.cols === -1 ? undefined : this.cols)}"
          minlength="${ifDefined(this.minlength === -1 ? undefined : this.minlength)}"
          maxlength="${ifDefined(this.maxlength === -1 ? undefined : this.maxlength)}"
          style=${styleMap(localStyles)}
          class="${classMap(classes)}"
          @change="${this._nativeChange}"
          @input="${this._nativeInput}"
          @focus="${this._nativeFocus}"
          @blur="${this._nativeBlur}"
          @keydown="${this._nativeKeyPress}"
          @keyup="${this._nativeKeyPress}"
          aria-labelledby="${ifDefined(this.getAriaLabelledBy())}"
        >
        </textarea>
        ${this.renderHelperText()}
      </div>
    `;
  }

  protected getAriaLabelledBy() {
    if (this.label) return this._labelId;
    if (this.ariaLabelledBy) return this.ariaLabelledBy;
    return undefined;
  }

  private async _nativeInput(e: Event) {
    e.stopPropagation();
    e.preventDefault();
    this.value = this.textareaElement.value;
    await this.updateComplete;
  }

  private async _nativeChange(e: Event) {
    e.stopPropagation();
    e.preventDefault();
    this.value = this.textareaElement.value;
    await this.updateComplete;
    this.dispatchEvent(
      new CustomEvent<TextareaChangePayload>('change', {
        detail: {value: this.value},
        bubbles: true,
        composed: true,
      })
    );
  }

  private async _nativeKeyPress(e: KeyboardEvent) {
    e.stopPropagation();
    await this.updateComplete;
    this.dispatchEvent(new KeyboardEvent(e.type, e));
  }
}

declare global {
  interface HTMLElementTagNameMap {
    'hds-textarea': Textarea;
  }
}
