import {useEffect, useImperativeHandle, useRef} from 'react';
import * as React from 'react';
import {NavBarSide, NavbarSideChangePayload} from '@here/hds-navbar';
import {HDSPanelProperties} from '../Panel/HDSPanel.js';

// eslint-disable-next-line no-new
new NavBarSide();

interface HDSNavBarSideProperties extends Omit<HDSPanelProperties, 'expanded' | 'onChange'> {
  collapsed?: boolean;
  title?: string;
  toggleText?: string;
  hideToggle?: Boolean;
  logo?: React.ReactNode;
  main?: React.ReactNode;
  footer?: React.ReactNode;
  onChange?: (e: NavbarSideChangePayload) => void;
}

const HDSNavBarSide = React.forwardRef((props: HDSNavBarSideProperties, ref) => {
  const {
    title,
    collapsed,
    logo,
    main,
    footer,
    className,
    toggleText,
    hideToggle,
    onChange,
    ...rest
  } = props;

  const innerRef = useRef<HTMLElement>(null);
  useImperativeHandle(ref, () => innerRef.current);

  const _handleChange = (e: CustomEvent<NavbarSideChangePayload>) => {
    if (e.target !== innerRef.current) return;
    onChange && onChange(e.detail);
  };

  useEffect(() => {
    if (innerRef.current) {
      if (onChange) {
        innerRef.current.addEventListener('change', _handleChange as EventListener);
      }
    }
    return () => {
      innerRef.current?.removeEventListener('change', _handleChange as EventListener);
    };
  }, [onChange, innerRef]);

  return (
    <hds-navbar-side
      ref={innerRef}
      {...(collapsed && {collapsed})}
      {...(hideToggle && {'hide-toggle': hideToggle})}
      {...(className && {class: className})}
      {...(toggleText && {'toggle-text': toggleText})}
      {...rest}
    >
      {logo && <div slot="logo">{logo}</div>}
      {title && <div slot="title">{title}</div>}
      {main && <div slot="main">{main}</div>}
      {footer && <div slot="footer">{footer}</div>}
    </hds-navbar-side>
  );
});

HDSNavBarSide.defaultProps = {
  toggleText: 'Hide Menu',
  hideToggle: false,
};

export default HDSNavBarSide;
