// eslint-disable-next-line no-use-before-define
import React, {useEffect, useImperativeHandle, useRef} from 'react';
import {Banner, BannerChangePayload, BannerVariant, BannerState} from '@here/hds-banner';

// eslint-disable-next-line no-new
new Banner();

interface HDSBannerProperties extends Omit<React.HTMLAttributes<HTMLElement>, 'onChange'> {
  message?: React.ReactNode;
  timeout?: number;
  variant?: BannerVariant;
  state?: BannerState;
  alignCenter?: boolean;
  removable?: boolean;
  open?: boolean;
  icon?: boolean;
  /* eslint-disable no-unused-vars */
  onClose?: (e: BannerChangePayload) => void;
  onTimeout?: (e: BannerChangePayload) => void;
  onChange?: (e: CustomEvent<BannerChangePayload>) => void;
  /* eslint-enable no-unused-vars */
}

const HDSBanner = React.forwardRef((props: HDSBannerProperties, ref) => {
  const {
    timeout,
    message,
    state,
    variant,
    alignCenter,
    removable,
    className,
    open,
    icon,
    children,
    onClose,
    onChange,
    onTimeout,
    ...rest
  } = props;
  const innerRef = useRef<HTMLElement>(null);
  useImperativeHandle(ref, () => innerRef.current);

  const _handleAction = (e: CustomEvent<BannerChangePayload>) => {
    if (e.target !== innerRef.current) return;
    switch (e.detail.from) {
      case 'button':
        onClose && onClose(e.detail);
        break;
      case 'timeout':
        onTimeout && onTimeout(e.detail);
        break;
    }
    onChange && onChange(e);
  };
  useEffect(() => {
    if (innerRef.current) {
      if (onClose || onTimeout || onChange) {
        innerRef.current.addEventListener('change', _handleAction as EventListener);
      }
    }
    return () => {
      innerRef.current?.removeEventListener('change', _handleAction as EventListener);
    };
  }, [onClose, onTimeout, onChange, innerRef]);

  return (
    <hds-banner
      variant={variant}
      state={state}
      {...(className && {class: className})}
      timeout={timeout}
      {...(removable && {removable})}
      {...(alignCenter && {'align-center': alignCenter})}
      {...(open && {open})}
      {...(icon && {icon})}
      ref={innerRef}
      {...rest}
    >
      {message && message}
      {children && children}
    </hds-banner>
  );
});

HDSBanner.defaultProps = {
  timeout: 0,
  variant: 'default',
  alignCenter: false,
  removable: false,
  icon: false,
};

export default HDSBanner;
