import {Dropdown, DropdownChangePayload, DropdownPlacement} from '@here/hds-dropdown';
import {List, ListSelectPayload} from '@here/hds-list';
import React, {useEffect, useImperativeHandle, useRef} from 'react';

/* eslint-disable no-new */
new Dropdown();
new List();
/* eslint-enable no-new */

interface HDSDropdownProperties
  extends Omit<React.HTMLAttributes<HTMLElement>, 'onSelect' | 'onChange'> {
  placement?: DropdownPlacement;
  allowedPlacements?: DropdownPlacement[];
  trigger?: React.ReactNode;
  menu?: React.ReactNode;
  open?: boolean;
  hideOnScroll?: boolean;
  /* eslint-disable no-unused-vars */
  onSelect?: (itemIndex: ListSelectPayload) => void;
  onChange?: (e: DropdownChangePayload) => void;
  /* eslint-enable no-unused-vars */
}

const HDSDropdown = React.forwardRef((props: HDSDropdownProperties, ref) => {
  const {
    open,
    hideOnScroll,
    placement,
    allowedPlacements,
    menu,
    trigger,
    onSelect,
    onChange,
    className,
    ...rest
  } = props;
  const innerRef = useRef<HTMLElement>(null);

  useImperativeHandle(ref, () => innerRef.current);

  const _handleSelected = (e: CustomEvent<ListSelectPayload>) => {
    if (e.target !== innerRef.current) return;
    onSelect && onSelect(e.detail);
  };

  const _handleChange = (e: CustomEvent<DropdownChangePayload>) => {
    if (e.target !== innerRef.current) return;
    onChange && onChange(e.detail);
  };

  useEffect(() => {
    if (innerRef.current) {
      if (onSelect) {
        innerRef.current.addEventListener('select', _handleSelected as EventListener);
      }
    }
    return () => {
      innerRef.current?.removeEventListener('select', _handleSelected as EventListener);
    };
  }, [onSelect, innerRef]);

  useEffect(() => {
    if (innerRef.current) {
      if (onChange) {
        innerRef.current.addEventListener('change', _handleChange as EventListener);
      }
    }
    return () => {
      innerRef.current?.removeEventListener('change', _handleChange as EventListener);
    };
  }, [onChange, innerRef]);

  // const allowedAutoPositionsValue =
  //   allowedAutoPositions && allowedAutoPositions.length
  //     ? allowedAutoPositions.join(',')
  //     : undefined;

  return (
    <hds-dropdown
      {...(open && {open})}
      {...(placement && {placement})}
      {...(className && {class: className})}
      {...(allowedPlacements &&
        allowedPlacements.length && {'allowed-placements': allowedPlacements.join(',')})}
      {...(hideOnScroll && {'hide-on-scroll': true})}
      {...rest}
      ref={innerRef}
    >
      {trigger && <span slot="trigger">{trigger}</span>}
      {menu && <div slot="menu">{menu}</div>}
    </hds-dropdown>
  );
});

HDSDropdown.defaultProps = {
  open: false,
  hideOnScroll: false,
  placement: 'bottom',
};

export default HDSDropdown;
