import React, {useEffect, useImperativeHandle, useRef} from 'react';
import {Breadcrumb, BreadcrumbChangePayload} from '@here/hds-breadcrumb';
// eslint-disable-next-line no-new
new Breadcrumb();

interface HDSBreadcrumbProperties extends Omit<React.HTMLAttributes<HTMLElement>, 'onChange'> {
  children: React.ReactNode;
  truncateItems?: boolean;
  truncateItemsBuffer?: number;
  onExpand?: (e: BreadcrumbChangePayload) => void;
  onChange?: (e: BreadcrumbChangePayload) => void;
}

const HDSBreadcrumb = React.forwardRef((props: HDSBreadcrumbProperties, ref) => {
  const {children, truncateItems, truncateItemsBuffer, className, onExpand, onChange, ...rest} =
    props;
  const innerRef = useRef<HTMLElement>(null);
  useImperativeHandle(ref, () => innerRef.current);

  const _handleChange = (e: CustomEvent<BreadcrumbChangePayload>) => {
    onExpand && onExpand(e.detail);
    onChange && onChange(e.detail);
  };

  useEffect(() => {
    if (innerRef.current && (onExpand || onChange)) {
      innerRef.current.addEventListener('change', _handleChange as EventListener);
    }
    return () => {
      innerRef.current?.removeEventListener('change', _handleChange as EventListener);
    };
  }, [onChange, onExpand, innerRef]);

  return (
    <hds-breadcrumb
      ref={innerRef}
      {...(truncateItems && {'truncate-items': truncateItems})}
      {...(truncateItemsBuffer && {
        'truncate-item-buffer': truncateItemsBuffer,
      })}
      {...(className && {class: className})}
      {...rest}
    >
      {children}
    </hds-breadcrumb>
  );
});

HDSBreadcrumb.defaultProps = {
  truncateItems: true,
  truncateItemsBuffer: 5,
};

export default HDSBreadcrumb;
