import {customElement, BaseElement} from '@here/hds-base';
import {html, nothing} from 'lit';
import {property} from 'lit/decorators.js';
import {ifDefined} from 'lit/directives/if-defined.js';
import {styles} from './hds-checkbox-group.css.js';

/**
 *
 * @slot default List of `hds-checkbox` elements
 */
@customElement('hds-checkbox-group')
export class CheckboxGroup extends BaseElement {
  static styles = styles;
  protected static id = 0;
  @property({type: String}) label: string | undefined;
  @property({type: String, attribute: 'secondary-label'}) secondaryLabel: string | undefined;
  @property({type: String, attribute: 'aria-label'}) ariaLabel: string = '';
  protected isTabbable = true;
  private labelId: string | undefined;

  private secondaryLabelId: string | undefined;

  connectedCallback() {
    super.connectedCallback();
    CheckboxGroup.id++;
    if (this.label) {
      this.labelId = `hds-checkbox-group-label-${CheckboxGroup.id}`;
    }
    if (this.secondaryLabel) {
      this.secondaryLabelId = `hds-checkbox-group-secondary-label-${CheckboxGroup.id}`;
    }
  }

  protected renderPrimaryLabel() {
    if (this.label) {
      return html` <label class="label-primary" id="${ifDefined(this.labelId)}">
        <div class="label-text label-text-primary">${this.label}</div>
      </label>`;
    }
    return nothing;
  }

  protected renderSecondaryLabel() {
    if (this.secondaryLabel) {
      return html` <label class="label-secondary" id="${ifDefined(this.secondaryLabelId)}">
        <div class="label-text label-text-secondary">${this.secondaryLabel}</div>
      </label>`;
    }
    return nothing;
  }

  protected renderLabels() {
    if (this.label || this.secondaryLabel) {
      return html` <div class="label-container clearfix">
        ${this.renderPrimaryLabel()} ${this.renderSecondaryLabel()}
      </div>`;
    }
    return nothing;
  }

  protected render() {
    const ariaLabelledBy = `${this.label ? this.labelId : ''} ${
      this.secondaryLabel ? this.secondaryLabelId : ''
    }`;
    return html` ${this.renderLabels()}
      <div
        class="-hds-checkbox-group-wrapper"
        role="group"
        .ariaLabel="${ifDefined(this.ariaLabel.trim())}"
        aria-labelledby="${ifDefined(ariaLabelledBy.trim())}"
      >
        <slot></slot>
      </div>`;
  }
}

declare global {
  interface HTMLElementTagNameMap {
    'hds-checkbox-group': CheckboxGroup;
  }
}
