import {customElement, BaseElement, findElementsInSlot, closestElement} from '@here/hds-base';
import '@here/hds-icon';
import {IconCategory} from '@here/hds-iconlibrary';
import {html, LitElement, nothing, PropertyValues} from 'lit';
import {property, query, state} from 'lit/decorators.js';
import {ClassInfo, classMap} from 'lit/directives/class-map.js';
import {ifDefined} from 'lit/directives/if-defined.js';
import {TabBar, TabBarVariant} from './hds-tab-bar.js';
import {TabPanelContainer} from './hds-tab-panel-container.js';
import {styles} from './hds-tab.css.js';

export type TabSelectPayload = string;

/**
 *
 * @slot default Content for the label
 *
 * @event {CustomEvent<string>} tabselect (**Internal event**) Fired when item is selected, internal use only `TabSelectPayload`
 */
@customElement('hds-tab')
export class Tab extends BaseElement {
  static tabIdCounter = 0;
  static styles = styles;
  static shadowRootOptions = {...LitElement.shadowRootOptions, delegatesFocus: true};
  isTabbable = true;
  @property({type: String}) icon = '';
  @property({type: String, attribute: 'icon-category'}) iconCategory: IconCategory = 'core-ui';
  @property({type: Boolean}) link = false;
  @property({type: Boolean, attribute: 'click-action'}) clickAction = false;
  @property({type: Boolean, attribute: 'external-link'}) externalLink = false;
  @property({type: Boolean}) disabled = false;
  @property({type: String, attribute: 'aria-controls'}) ariaControls;
  @property({type: String, attribute: 'aria-role'}) ariaRole = 'tab';
  @property({type: String, attribute: 'tab-panel-id'}) tabPanelId = '';
  @property({type: Boolean, reflect: true}) selected = false;
  @property({type: String, attribute: false}) variant: TabBarVariant = 'default';
  @property({type: Boolean}) chevron = false;

  @state() rightSideIcon = '';
  @state() showMenu = false;
  @query('#menu') protected menuItemsSlot!: HTMLSlotElement;
  @query('slot:not([name])', false) protected defaultSlot!: HTMLSlotElement;

  get clickActionValue(): boolean {
    return this.clickAction;
  }

  showSelectedTabMenu(showMenu: boolean): void {
    this.showMenu = showMenu;

    if (this.hasMenuContent()) {
      this.rightSideIcon = showMenu ? 'chevron-up' : 'chevron-down';
    } else {
      this.rightSideIcon = '';
    }
  }

  protected updated(_changedProperties: PropertyValues) {
    if (this.selected && _changedProperties.has('selected')) {
      const tabBar = closestElement<TabBar>('hds-tab-bar', this);
      const tabPanelContainer = this.ownerDocument.querySelector(
        `[tab-bar-id="${tabBar!.id}"]`
      ) as TabPanelContainer;
      if (tabPanelContainer) {
        tabPanelContainer.selectedPanel = this.tabPanelId;
      }
    }
  }

  protected renderIcon(_icon: string, _category: IconCategory) {
    if (!_icon) {
      return nothing;
    }
    return html` <hds-icon name="${_icon}" size="16px" category="${_category}"></hds-icon>`;
  }

  protected renderIconRight(_icon: string, _category: IconCategory) {
    if (!_icon || (!this.chevron && _icon.startsWith('chevron-'))) {
      return nothing;
    }
    return html` <hds-icon name="${_icon}" size="16px" category="${_category}"></hds-icon>`;
  }

  protected firstUpdated() {
    this.id = this.id || `hds-tab-${++Tab.tabIdCounter}`;
    if (this.hasMenuContent()) {
      this.rightSideIcon = 'chevron-down';
    } else if (this.link && this.externalLink) {
      this.rightSideIcon = 'external-link';
    } else {
      this.rightSideIcon = '';
    }
  }

  protected render() {
    const classes = {
      tab: true,
      disabled: this.disabled,
      'hds-tab--icon-label': this.icon,
      'hds-tab--icon-actionable': this.rightSideIcon,
      'external-link': this.externalLink,
    };
    classes[`hds-tab--variant-${this.variant}`] = true;
    const slotClasses = {
      'hds-tab-menu-wrapper--show-menu': this.showMenu,
    };
    // eslint-disable-next-line  lit-a11y/mouse-events-have-key-events
    return html`
      <div
        class="hds-tab-wrapper"
        @click="${this._handleClick}"
        @keydown="${this._handleKeydown}"
        @mouseover="${this._handleMouseMove}"
        @mouseout="${this._handleMouseMove}"
      >
        ${this.link ? this._renderAsLink(classes) : this._renderAsButton(classes)}
        <slot id="menu" class="hds-tab-menu-wrapper ${classMap(slotClasses)}" name="menu"></slot>
      </div>
    `;
  }

  private _handleKeydown = (e: KeyboardEvent) => {
    switch (e.key) {
      case 'Enter':
      case ' ':
        this.showMenu = true;
        break;
      case 'Tab':
        this.showMenu = false;
        break;
    }
  };

  private hasMenuContent(): boolean {
    return this.menuItemsSlot.assignedNodes({flatten: true}).length !== 0;
  }

  private _renderAsLink(classes: ClassInfo) {
    return html`
      <div
        class="${classMap(classes)}"
        role="${ifDefined(this.ariaRole)}"
        aria-selected="${ifDefined(this.selected)}"
        aria-disabled="${ifDefined(this.disabled)}"
        aria-controls="${ifDefined(this.ariaControls)}"
        ?disabled="${this.disabled}"
      >
        ${this.renderIcon(this.icon, this.iconCategory)}
        <slot></slot>
        ${this.renderIconRight(this.rightSideIcon, 'core-ui')}
      </div>
    `;
  }

  private _renderAsButton(classes: ClassInfo) {
    return html`
      <button
        class="${classMap(classes)}"
        role="${ifDefined(this.ariaRole)}"
        aria-selected="${ifDefined(this.selected)}"
        aria-disabled="${ifDefined(this.disabled)}"
        aria-controls="${ifDefined(this.ariaControls)}"
        ?disabled="${this.disabled}"
      >
        ${this.renderIcon(this.icon, this.iconCategory)}
        <slot></slot>
        ${this.renderIconRight(this.rightSideIcon, 'core-ui')}
      </button>
    `;
  }

  private async _handleClick(e: any) {
    if (this.disabled) return;
    await this.updateComplete;

    this.dispatchEvent(
      new CustomEvent<TabSelectPayload>('tabselect', {detail: this.id, bubbles: true})
    );

    if (this.link && this.externalLink && e && e.target && e.target.nodeName !== 'A') {
      e.preventDefault();
      e.stopPropagation();
      const anchor = findElementsInSlot<HTMLAnchorElement>(this.defaultSlot, HTMLAnchorElement)[0];
      if (anchor) {
        anchor.click();
      }
    }
  }

  private _handleMouseMove(e: MouseEvent): void {
    if (this.clickAction) {
      return;
    }

    if (this.rightSideIcon && !this.link) {
      e.preventDefault();

      switch (e.type) {
        case 'mouseout':
          if (this.showMenu) {
            this.showMenu = false;
            this.rightSideIcon = 'chevron-down';
          }
          break;
        case 'mouseover':
          if (!this.showMenu) {
            this.showMenu = true;
            this.rightSideIcon = 'chevron-up';
          }
          break;
      }
    }
  }
}

declare global {
  interface HTMLElementTagNameMap {
    'hds-tab': Tab;
  }
}
