/* eslint max-classes-per-file: ["error", 2] */
import {BaseElement, customElement, isSlotEmpty} from '@here/hds-base';
import '@here/hds-icon';
import {IconCategory} from '@here/hds-iconlibrary';
import {html, LitElement, nothing, PropertyValues} from 'lit';
import {property, query, state} from 'lit/decorators.js';
import {classMap} from 'lit/directives/class-map.js';
import {styles} from './hds-switcher-tab.css.js';
import {Switcher, SwitcherSize, SwitcherVariant} from './hds-switcher.js';

export type SwitcherTabSelectPayload = string;

/**
 * @slot default Content
 *
 * @event {CustomEvent<string>} select Fired when the element has been selected `SwitcherTabSelectPayload`
 */
@customElement('hds-switcher-tab')
export class SwitcherTab extends BaseElement {
  static shadowRootOptions = {...LitElement.shadowRootOptions, delegatesFocus: true};
  static styles = styles;
  static switcherTabIdCounter = 0;

  private _disabled = false;

  @property({type: Boolean, reflect: true}) selected = false;
  @property({type: String}) icon?: string;
  @property({type: String, attribute: 'icon-right'}) iconRight?: string;
  @property({type: String, attribute: 'icon-category'}) iconCategory: IconCategory = 'core-ui';
  @property({type: String, attribute: 'icon-right-category'}) iconRightCategory: IconCategory =
    'core-ui';
  @state() switcherVariant: SwitcherVariant = 'subtle';
  @state() switcherSize: SwitcherSize = 'medium';
  @state() switcherDisabled = false;
  @state() isLastChild = false;
  @state() isFirstChild = false;
  @state() protected slotHasContent = false;
  @query('slot:not([name])') protected defaultSlot!: HTMLSlotElement;
  @query('button') buttonElement!: HTMLButtonElement;

  get disabled() {
    return this._disabled || this.switcherDisabled;
  }

  @property({type: Boolean})
  set disabled(value: boolean) {
    const oldValue = this._disabled;
    this._disabled = value;
    this.requestUpdate('disabled', oldValue);
  }

  focus() {
    return this.buttonElement && this.buttonElement.focus();
  }

  blur() {
    return this.buttonElement && this.buttonElement.blur();
  }

  click() {
    if (!this.disabled) {
      this.buttonElement.click();
    }
  }

  private _onClick() {
    this.selected = true;
  }

  protected firstUpdated() {
    this.id = this.id || `hds-switcher-tab-${++SwitcherTab.switcherTabIdCounter}`;
    this.onSlotChange();
  }

  protected updated(_changedProperties: PropertyValues) {
    const isSelectedChanged = this.selected && _changedProperties.get('selected') !== undefined;
    const isDisabledChanged = this.disabled && _changedProperties.get('disabled') !== undefined;
    if (isSelectedChanged) {
      this.dispatchEvent(
        new CustomEvent<SwitcherTabSelectPayload>('select', {
          detail: this.id,
          bubbles: true,
          composed: true,
        })
      );
    }

    if (isDisabledChanged && this.parentNode instanceof Switcher) {
      this.parentNode.selectFirstNotDisabled();
    }
  }

  protected renderIcon() {
    if (this.icon) {
      return html`<hds-icon
        name="${this.icon}"
        size="16px"
        category="${this.iconCategory}"
      ></hds-icon>`;
    }

    return nothing;
  }

  protected renderIconRight() {
    if (this.iconRight) {
      return html`<hds-icon
        name="${this.iconRight}"
        size="16px"
        category="${this.iconRightCategory}"
      ></hds-icon>`;
    }

    return nothing;
  }

  protected async onSlotChange() {
    this.slotHasContent = !isSlotEmpty(this.defaultSlot);
  }

  protected render() {
    const classes = {
      '-hds-switcher-tab-wrapper': true,
      'slot-has-content': this.slotHasContent,
      icon: !!this.icon,
      'icon-right': !!this.iconRight,
      'last-child': this.isLastChild,
      'first-child': this.isFirstChild,
    };
    classes[`hds-switcher-tab-variant-${this.switcherVariant}`] = true;
    classes[`hds-switcher-tab-size-${this.switcherSize}`] = true;
    classes[`${this.selected ? 'selected' : 'not-selected'}`] = true;
    classes[`${this.disabled ? 'disabled' : 'not-disabled'}`] = true;

    const isAreaChecked = !this.disabled && !this.switcherDisabled && this.selected;

    return html`
      <button
        .ariaLabel="${this.ariaLabel ?? this.icon}"
        .ariaChecked="${isAreaChecked.toString()}"
        .role="${'radio'}"
        class="${classMap(classes)}"
        ?disabled="${this.disabled}"
        @click="${this._onClick}"
      >
        ${this.renderIcon()}
        <span class="content">
          <slot @slotchange="${this.onSlotChange}"></slot>
        </span>
        ${this.renderIconRight()}
      </button>
    `;
  }
}

declare global {
  interface HTMLElementTagNameMap {
    'hds-switcher-tab': SwitcherTab;
  }
}
