import React, {useEffect, useImperativeHandle} from 'react';
import {Panel, PanelChangePayload} from '@here/hds-panel';

// eslint-disable-next-line no-new
new Panel();

export interface HDSPanelProperties extends Omit<React.HTMLAttributes<HTMLElement>, 'onChange'> {
  children?: React.ReactNode;
  variant?: string;
  panelPosition?: string;
  panelInteraction?: string;
  partialSize?: number;
  expanded?: string;
  onChange?: (value: PanelChangePayload) => void;
}

const HDSPanel = React.forwardRef((props: HDSPanelProperties, ref) => {
  const {
    variant,
    panelPosition,
    panelInteraction,
    partialSize,
    expanded,
    children,
    className,
    onChange,
    ...rest
  } = props;

  const innerRef = React.useRef<HTMLElement>(null);
  useImperativeHandle(ref, () => innerRef.current);

  const _handleChange = (e: CustomEvent<PanelChangePayload>) => {
    if (e.target !== innerRef.current) return;
    onChange && onChange(e.detail);
  };

  useEffect(() => {
    if (onChange && innerRef.current) {
      innerRef.current.addEventListener('change', _handleChange as EventListener);
    }
    return () => innerRef.current?.removeEventListener('change', _handleChange as EventListener);
  }, [onChange, innerRef]);

  const attributes: HDSPanelProperties = {};

  variant && (attributes.variant = variant);
  panelInteraction && (attributes['panel-interaction'] = panelInteraction);
  panelPosition && (attributes['panel-position'] = panelPosition);
  partialSize && (attributes['partial-size'] = partialSize);
  expanded && (attributes.expanded = expanded);

  return (
    <hds-panel {...attributes} {...(className && {class: className})} {...rest} ref={innerRef}>
      {children}
    </hds-panel>
  );
});

HDSPanel.defaultProps = {
  variant: 'vertical',
  panelPosition: 'left',
  panelInteraction: 'none',
  partialSize: 0,
  expanded: 'open',
};

export default HDSPanel;
